import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ModelDeploymentService {
  serverBaseUrl = environment.serverBaseUrl;

  constructor(private _htttp: HttpClient) { }

  /**
   * Fetches the host list from DB
   */
  getHostsList(): Observable<any> {
    return this._htttp.get(`${this.serverBaseUrl}/api/settings/general`);
  }

  /**
   * Creates a new host record
   * @param hostData 
   */
  createNewHost(hostData: any): Observable<any> {
    return this._htttp.post(`${this.serverBaseUrl}/api/settings/general`, hostData);
  }

  /**
   * Updates the host details
   * @param hostData 
   * @param hostId 
   */
  updateHostDetails(hostData: any, hostId: string): Observable<any> {
    return this._htttp.put(`${this.serverBaseUrl}/api/settings/${hostId}/general`, hostData);
  }

  /**
   * Deletes the host from DB
   * @param hostData 
   * @param hostId 
   */
  deleteHost(hostId: string): Observable<any> {
    return this._htttp.delete(`${this.serverBaseUrl}/api/settings/${hostId}/general`);
  }
}
