import { SpinnerService } from 'src/app/shared/spinner.service';
import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ThemePalette } from '@angular/material/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { PscoreNodesService } from './pscore-nodes.service';
const swal = require('sweetalert');
@Component({
  selector: 'app-pscore-nodes',
  templateUrl: './pscore-nodes.component.html',
  styleUrls: ['./pscore-nodes.component.scss'],
})
export class PscoreNodesComponent implements OnInit {
  dataSource: MatTableDataSource<[]>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild('myTable') table: MatTable<any>;

  pageSizeOptions: Array<number> = [3, 5, 10, 15, 20];
  displayedColumns: Array<string> = [];

  /** Subscription variables */
  nodeSub$: Subscription;

  // Progress spinner settings
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;

  nodeForm: FormGroup;
  dialogRef: MatDialogRef<any>;
  showSpinner: Boolean = false;

  constructor(
    private _fb: FormBuilder,
    private _node: PscoreNodesService,
    private _toaster: ToastrService,
    private _dialog: MatDialog,
    private _spinner: SpinnerService
  ) {
    this.displayedColumns = ['srNo', 'machineName', 'machineId', 'address', 'actions'];
    this.nodeForm = this._fb.group({
      machineName: ['', Validators.required],
    });
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getNodesList();
  }

  /**
   * Fetches the pscore nodes list
   */
  getNodesList() {
    this._node.getPscoreNodeList().subscribe(
      (res: any) => {
        if (res) {
          // Assign the data to the data source for the table to render
          this.dataSource = new MatTableDataSource(res);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          this._toaster.error('Cannot fetch nodes list', 'PSCORE Node Setting');
        }
        this._spinner.stopSpinner();
      },
      (err: Error) => {
        this._spinner.stopSpinner();
        this._toaster.error('Server Error', 'PSCORE Node Setting');
      }
    );
  }

  /**
   * Opens the pscore node modal to create new node
   * @param hostTemplate
   */
  openNodeModal(hostTemplate: TemplateRef<any>) {
    this.dialogRef = this._dialog.open(hostTemplate, {
      height: 'auto',
      width: '50%',
    });
  }

  /**
   * Creates a new node record into DB
   * @param formData node details
   */
  onSubmitNode(formData: any): void {
    this.showSpinner = true;
    this.nodeSub$ = this._node.createNewNode(formData).subscribe(
      (res) => {
        this.showSpinner = false;
        if (res) {
          this.getNodesList();
        } else {
          this._toaster.error('Cannot create new node', 'PSCORE Node Setting');
        }
      },
      (err) => {
        this.showSpinner = false;
        this._toaster.error('Server Error', 'PSCORE Node Setting');
      }
    );
  }

  /**
   * Deletes the selected pscore node
   * @param hostData
   */
  deleteNode(nodeData: any): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this node!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        this.nodeSub$ = this._node.deleteNode(nodeData._id).subscribe(
          (res: any) => {
            this.getNodesList();
            this._toaster.success('Node deleted successfully');
          },
          (err: any) => {
            this._toaster.error('Server Error', 'PSCORE Nodes');
          }
        );
      }
    });
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.nodeSub$) {
      this.nodeSub$.unsubscribe();
    }
  }
}
