import { EdaReportComponent } from '../../../shared/components/eda-report/eda-report.component';
import { SpinnerService } from '../../../shared/spinner.service';
import { ReportsService } from './reports.service';
import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { Subject, Subscription } from 'rxjs';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { ThemePalette } from '@angular/material/core';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { takeUntil } from 'rxjs/operators';
import { EdaService } from '../../eda/eda.service';
import { AnonymousSubject } from 'rxjs-compat';

@Component({
  selector: 'app-reports',
  templateUrl: './reports.component.html',
  styleUrls: ['./reports.component.scss'],
})
export class ReportsComponent implements OnInit {
  dataSource: MatTableDataSource<[]>;
  dataSourceForFile: MatTableDataSource<[]>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatPaginator) paginatorForFile: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatSort) sortForFile: MatSort;
  @ViewChild('reportTemplate', { static: true }) template: TemplateRef<any>;
  unsubscribe$ = new Subject<boolean>();
  pageSizeOptions: Array<number> = [3, 5, 10, 15, 20];
  pageSizeOptionsFile: Array<number> = [100, 150, 250, 500];
  displayedColumns: Array<string> = [];
  fileColumnsToDisplay: Array<string> = [];
  edaData: any;
  /** Subscription variables */
  reportSub$: Subscription;

  // Progress spinner settings
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;

  dialogRef: MatDialogRef<any>;
  outputExt: string;
  selectedReportData: any;
  showSpinner: boolean = false;
  imageData: SafeResourceUrl;
  reportImage: any;

  constructor(
    private _toaster: ToastrService,
    private _report: ReportsService,
    private _dialog: MatDialog,
    private _spinner: SpinnerService,
    private _sanitizer: DomSanitizer,
    private _eda: EdaService
  ) {
    // this._spinner.showSpinner();
    this.displayedColumns = ['srNo', 'hostName', 'hostUrl', 'createdAt', 'actions'];
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getReportsList();
  }

  /**
   * Fetches the reports list from the DB
   */
  getReportsList(): void {
    this.reportSub$ = this._report.getReportsList().subscribe(
      (res) => {
        if (res) {
          this._spinner.stopSpinner();
          /** Assign the data to the data source for the table to render */
          this.dataSource = new MatTableDataSource(res);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          this._spinner.stopSpinner();
          this._toaster.error('Cannot fetch reports list', 'Reports');
        }
      },
      (err) => {
        this._spinner.stopSpinner();
        this._toaster.error('Server Error', 'Reports');
      }
    );
  }

  /**
   * Opens the report modal to preview the report
   */
  openReportModal(): void {
    this.dialogRef = this._dialog.open(this.template, {
      height: 'auto',
      width: '80%',
    });
  }

  /**
   * Generates the data to be view
   * @param reportData
   */
  async handleReportPreview(reportData: any): Promise<any> {
    let file = reportData.outputId.filename;
    this.outputExt = file.substring(file.lastIndexOf('.') + 1);

    if (reportData.outputType === 'EDA') {
      await this.openEdaReport(reportData);
    } else {
      if (
        this.outputExt === 'pdf' ||
        this.outputExt === 'png' ||
        this.outputExt === 'jpg' ||
        this.outputExt === 'tiff'
      ) {
        this.openPreview(reportData);
      } else if (
        this.outputExt === 'csv' ||
        this.outputExt === 'xls' ||
        this.outputExt === 'xlsx'
      ) {
        this.openFilePreview(reportData);
      }
    }
  }

  /**
   * If the report type is EDA, then preview the EDA report in modal
   * @param reportData
   */
  async openEdaReport(edaReportData: any): Promise<void> {
    this._spinner.showSpinner();
    this.edaData = await this.getEdaData(edaReportData).catch((e: Error) => {
      this._toaster.error(`Error - ${e.message}`, 'Eda');
    });
    this._spinner.stopSpinner();
    const reportData = {
      projectId: edaReportData.projectId._id,
      edaId: this.edaData._id,
      currentEdaId: this.edaData._id,
      currentPipelineFilename: this.edaData.tasks[0].currentPipelineFilename,
    };
    /** Opens the eda report component modal to render report */
    this.dialogRef = this._dialog.open(EdaReportComponent, {
      height: 'auto',
      width: '80%',
      data: {
        moduleName: 'reports',
        edaReportData: reportData,
      },
    });
  }

  async getEdaData(edaReportData): Promise<any> {
    return this._eda
      .getEdaData(edaReportData.outputId.edaId)
      .pipe(takeUntil(this.unsubscribe$))
      .toPromise();
  }
  /**
   * Opens the preview for output of type pdf, jpg, png and tiff extenstion
   * @param reportData
   */
  openPreview(reportData: any): void {
    this.showSpinner = true;
    this.getImageBase(reportData);
    this.selectedReportData = reportData;
    this.openReportModal();
  }

  /**
   * Opens the preview for output of type csv, xls, or xlsx extenstion
   * @param reportData
   */
  openFilePreview(reportData: any): void {
    this.showSpinner = true;
    this.selectedReportData = reportData;
    this.openReportModal();
    let finalData = {
      isDataTypeRequired: true,
      projectId: reportData.outputId.projectId,
      filename: reportData.outputId.filename,
      createdBy: reportData.outputId.createdBy,
    };
    this._report
      .getFilePreview(finalData, reportData.outputId.projectId, reportData.outputId._id)
      .subscribe(
        (res: any) => {
          if (res) {
            this.showSpinner = false;

            /** Fetches the key from the reponse for displaying columns */
            let head: Array<string> = Object.keys(res.preview[0]);
            head.forEach((col) => {
              this.fileColumnsToDisplay.push(col);
            });

            /** Assign the data to the data source for the table to render */
            this.dataSourceForFile = new MatTableDataSource(res.preview);
            this.dataSourceForFile.paginator = this.paginatorForFile;
            this.dataSourceForFile.sort = this.sortForFile;
          } else {
            this.showSpinner = false;
            this._toaster.error('Cannot fetch the file preview details', 'Report');
          }
        },
        (err: Error) => {
          this.showSpinner = false;
          this._toaster.error('Server Error', 'Report');
        }
      );
  }

  /**
   * Fetches the base64 encode for type pdf, png, jpg and tiff
   * @param reportData
   */
  getImageBase(reportData: any): void {
    let finalData: Object = {
      projectId: reportData.outputId.projectId,
      filename: reportData.outputId.filename,
      createdBy: reportData.outputId.createdBy,
    };

    this._report.getImageEncode(finalData, reportData.outputId.projectId).subscribe(
      (res: any) => {
        let base64: string;
        let safeUrl: SafeResourceUrl;

        switch (this.outputExt) {
          case 'pdf':
            base64 = `data:application/pdf;base64,${res.data}`;
            safeUrl = this._sanitizer.bypassSecurityTrustResourceUrl(base64);
            this.imageData = safeUrl;
            break;
          case 'png':
            base64 = `data:image/png;base64,${res.data}`;
            safeUrl = this._sanitizer.bypassSecurityTrustResourceUrl(base64);
            this.reportImage = safeUrl;
            break;
          case 'jpg':
            base64 = `data:image/jpg;base64,${res.data}`;
            safeUrl = this._sanitizer.bypassSecurityTrustResourceUrl(base64);
            this.reportImage = safeUrl;
            break;
          case 'tiff':
            base64 = `data:image/tiff;base64,${res.data}`;
            safeUrl = this._sanitizer.bypassSecurityTrustResourceUrl(base64);
            this.reportImage = safeUrl;
            break;
        }

        this.showSpinner = false;
      },
      (err: Error) => {
        this.showSpinner = false;
        this._toaster.error('Server Error', 'Report');
      }
    );
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.reportSub$) {
      this.reportSub$.unsubscribe();
    }
  }
}
