import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ThemePalette } from '@angular/material/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { DashboardService } from '../../dashboard.service';
import { UsecaseListService } from '../usecase-list.service';
@Component({
  selector: 'app-usecase',
  templateUrl: './usecase.component.html',
  styleUrls: ['./usecase.component.scss'],
})
export class UsecaseComponent implements OnInit {
  dataSource: MatTableDataSource<[]>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild('myTable') table: MatTable<any>;
  pageSizeOptions: Array<number> = [3, 5, 10, 15, 20];
  displayedColumns: Array<string> = [];

  /** Subscription letiables */
  usecaseSub$: Subscription;

  /** Progress spinner settings */
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;
  isEdit: Boolean = false;
  usecaseForm: FormGroup;
  dialogRef: MatDialogRef<any>;
  showSpinner: boolean = false;
  usecaseIdToUpdate: string;
  domainList: any;
  projectList: any;
  panelOpenState = true;
  showPersona: boolean = false;
  trainingList: any;
  modelList: any;
  independentFeature: any;
  itemsArray: FormArray;
  formInput: any = [];
  idealPersona: any = {};
  outcomeForm: FormGroup;
  outcomeTransform: { outcome: '' }[];
  outcome: any = [];
  constructor(
    private _usecaseList: UsecaseListService,
    private _toaster: ToastrService,
    private _fb: FormBuilder,
    private _dialog: MatDialog,
    private _dashboard: DashboardService
  ) {
    this.displayedColumns = ['srNo', 'domain', 'title', 'modelId', 'createdAt', 'actions'];
    this.getUsecaseList();
    this.usecaseForm = this._fb.group({
      title: ['', Validators.required],
      domain: ['', Validators.required],
      tag: ['', Validators.required],
      objectives: ['', Validators.required],
      description: ['', Validators.required],
      outcome: ['', Validators.required],
      projectId: ['', Validators.required],
      trainingId: ['', Validators.required],
      feature: ['', Validators.required],
      modelId: ['', Validators.required],
    });

    //form array for outcome
    this.outcomeForm = this._fb.group({
      itemsArray: this._fb.array([this.createItem()]),
    });
  }

  ngOnInit(): void {
    this.getDomainList();
  }

  /**
   * Adds the new item in the form array
   */
  addItem(): void {
    this.itemsArray = this.outcomeForm.get('itemsArray') as FormArray;
    this.itemsArray.push(this.createItem());
  }

  /**
   * Removes the item from form array
   */
  removeItem(index: number): void {
    this.itemsArray.removeAt(index);
  }

  /**
   * Creates and returns a new form structure
   * @returns
   */
  createItem(): FormGroup {
    return this._fb.group({
      outcome: '',
    });
  }
  /**
   * Opens the usecase modal
   * @param UsecaseTemplate
   */
  async openUsecaseModal(UsecaseTemplate: TemplateRef<any>, usecaseData?: any) {
    this.showPersona = false;
    this.idealPersona = [];
    this.getAllProjects();
    if (usecaseData) {
      this.isEdit = true;
      this.outcome = [];
      this.onProjectChange(usecaseData.projectId._id);
      setTimeout(() => {
        this.onTrainingChange(usecaseData.trainingId._id);
        let featureArray = [];
        usecaseData.dataInfo.forEach((element) => {
          let itemFound = this.independentFeature.filter((item) => {
            return item.colName == element.colName;
          });
          if (itemFound.length) {
            var index = this.independentFeature.findIndex(function (x) {
              return x.colName == itemFound[0].colName;
            });
            this.independentFeature.splice(index, 1);
          }
        });
        featureArray = featureArray.concat(usecaseData.dataInfo).concat(this.independentFeature);
        this.independentFeature = [];
        this.independentFeature = featureArray;
      }, 1000);
      for (let key in usecaseData.outcome) {
        this.outcome.push({ outcome: usecaseData.outcome[key] });
      }
      this.usecaseIdToUpdate = usecaseData._id;
      this.usecaseForm.patchValue({
        domain: usecaseData.domain,
        title: usecaseData.title,
        tag: usecaseData.tag,
        description: usecaseData.description,
        objectives: usecaseData.objectives,
        projectId: usecaseData.projectId._id,
        modelId: usecaseData.modelId._id,
        trainingId: usecaseData.trainingId._id,
      });
      if (usecaseData.idealPersona) {
        this.showPersona = true;
        this.idealPersona.title = usecaseData.idealPersona.title;
        this.idealPersona.description = usecaseData.idealPersona.description;
      } else {
        this.showPersona = false;
      }
      this.outcomeForm.setControl('itemsArray', this.prefillForm(this.outcome));
    } else {
      this.usecaseForm.reset();
      this.outcomeForm.reset();
      this.isEdit = false;
    }
    this.dialogRef = this._dialog.open(UsecaseTemplate, {
      minWidth: '50vw',
    });
  }

  /**
   * set outcome for edit option in form array
   * @param outcomeForm
   * @returns
   */
  prefillForm(outcomeForm): FormArray {
    const formArray = new FormArray([]);
    outcomeForm.forEach((element) => {
      formArray.push(
        this._fb.group({
          outcome: element.outcome,
        })
      );
    });
    return formArray;
  }
  /**
   * Fetches all project list from DB by triggering API
   */
  getAllProjects() {
    this.showSpinner = true;
    this.usecaseSub$ = this._dashboard.getAllProjectsList().subscribe(
      (res: any) => {
        if (res) {
          this.projectList = res;
          this.showSpinner = false;
        }
      },
      (err) => {
        this.showSpinner = false;
      }
    );
  }

  /**
   * Fetches all training list of particular project from DB by trigerring API
   * @param projectId
   */
  async onProjectChange(projectId) {
    this.showSpinner = true;
    this.usecaseSub$ = this._usecaseList.getTrainingList(projectId).subscribe(
      (res: any) => {
        if (res) {
          this.trainingList = res;
          this.showSpinner = false;
        }
      },
      (err) => {
        this.showSpinner = false;
      }
    );
  }

  /**
   * Fetches all models for a particular training from DB
   * @param trainId
   */
  onTrainingChange(trainId) {
    this.independentFeature = this.trainingList.filter((item) => item._id == trainId);
    if (this.independentFeature[0].problemType == 'timeseries') {
      this.independentFeature = [{ colName: this.independentFeature[0].depVariable }];
    } else if (this.independentFeature[0].problemType == 'clustering') {
      this.independentFeature = this.independentFeature[0].indepVariable;
    } else {
      this.independentFeature = this.independentFeature[0].indepVariable.concat({
        colName: this.independentFeature[0].depVariable,
      });
    }
    this.showSpinner = true;
    this.usecaseSub$ = this._usecaseList.getModelList(trainId).subscribe(
      (res: void) => {
        this.modelList = res;
        this.showSpinner = false;
      },
      (err) => {
        this.showSpinner = false;
      }
    );
  }
  /**
   * Function to check for the create or edit option of usecase
   * @param formData
   */
  onSubmitUsecase(formData: any): void {
    this.isEdit ? this.updateUsecase(formData) : this.createNewUsecase(formData);
  }

  /**
   * Creates a new usecase
   * @param formData
   */
  createNewUsecase(formData: any) {
    this.showSpinner = true;
    let data = this.independentFeature.filter(
      (item) => item.description && item.description !== undefined
    );
    formData.dataInfo = data;
    let outcome = this.outcomeForm.value.itemsArray.map((item) => {
      return item.outcome;
    });
    formData.outcome = outcome;
    formData.usecaseType = 'default';
    this.usecaseSub$ = this._usecaseList.createNewUsecase(formData).subscribe(
      (res: any) => {
        this.showSpinner = false;
        if (res) {
          this._toaster.success('Usecase created successfully', 'Usecase Setting');
          this.getUsecaseList();
        } else {
          this._toaster.error('Cannot create new usecase', 'Usecase Setting');
        }
      },
      (err: any) => {
        this.showSpinner = false;
        this._toaster.error('Server Error', 'Usecase Setting');
      }
    );
  }

  /**
   * Fetches the usecase list
   */
  getUsecaseList(): void {
    this.usecaseSub$ = this._usecaseList.getUsecaseList().subscribe(
      (res: any) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          this._toaster.error('Cannot fetch the usecase list', 'Usecase Setting');
        }
      },
      (err: any) => {
        this._toaster.error('Server Error', 'Usecase Setting');
      }
    );
  }
  /**
   * Updates the usecase details
   * @param formData
   */
  updateUsecase(formData: any) {
    this.showSpinner = true;
    formData.dataInfo = this.independentFeature;
    let outcome = this.outcomeForm.value.itemsArray.map((item) => {
      return item.outcome;
    });
    formData.outcome = outcome;
    let usecase = {
      usecase: formData,
    };
    this.usecaseSub$ = this._usecaseList.updatedUsecase(usecase, this.usecaseIdToUpdate).subscribe(
      (res: any) => {
        this.showSpinner = false;
        if (res) {
          this._toaster.success('Usecase updated successfully', 'Usecase Setting');
          this.getUsecaseList();
        } else {
          this._toaster.error('Cannot update usecase', 'Usecase Setting');
        }
      },
      (err: any) => {
        this.showSpinner = false;
        this._toaster.error('Server Error', 'Usecase Setting');
      }
    );
  }
  /**
   * Deletes the usecase
   * @param usecaseData
   */
  deleteUsecase(usecaseData: any): void {
    this.usecaseSub$ = this._usecaseList.deleteUsecase(usecaseData._id).subscribe(
      (res: any) => {
        if (res) {
          this.getUsecaseList();
          this._toaster.success('Usecase deleted successfully');
        } else {
          this._toaster.error('Cannot delete usecase', 'Usecase Setting');
        }
      },
      (err: any) => {
        this._toaster.error('Server Error', 'Usecase Setting');
      }
    );
  }

  /**
   * Fetches the domain list
   */
  getDomainList(): void {
    this.usecaseSub$ = this._usecaseList.getDomainList().subscribe(
      (res: any) => {
        if (res) {
          this.domainList = res;
        } else {
          this._toaster.error('Cannot fetch the domain list', 'Usecase Setting');
        }
      },
      (err: any) => {
        this._toaster.error('Server Error', 'Usecase Setting');
      }
    );
  }

  onModelChange(modelId) {
    let model = this.modelList.filter((item) => item._id == modelId);
    if (model[0].hasOwnProperty('idealPersona')) {
      this.showPersona = true;
    } else {
      this.showPersona = false;
    }
  }
  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.usecaseSub$) {
      this.usecaseSub$.unsubscribe();
    }
  }
}
