import { SpinnerService } from 'src/app/shared/spinner.service';
import { SchedularService } from './schedular.service';
import { Component, OnInit, ViewChild, OnDestroy, TemplateRef } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { Subscription } from 'rxjs';
import { ToastrService } from 'ngx-toastr';
import { ThemePalette } from '@angular/material/core';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
const swal = require('sweetalert');
const moment = require('moment');

@Component({
  selector: 'app-schedular',
  templateUrl: './schedular.component.html',
  styleUrls: ['./schedular.component.scss'],
})
export class SchedularComponent implements OnInit, OnDestroy {
  dataSource: MatTableDataSource<[]>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild('myTable') table: MatTable<any>;

  pageSizeOptions: Array<number> = [3, 5, 10, 15, 20];
  displayedColumns: Array<string> = [];

  /** Subscription letiables */
  schedularSub$: Subscription;

  /** Progress spinner settings */
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;

  isEdit: Boolean = false;
  schedularForm: FormGroup;
  dialogRef: MatDialogRef<any>;
  showSpinner: boolean = false;
  schedularIdToUpdate: string;
  recurType: string = 'Minute';
  recurCountArray: Array<string>;
  recurCount: string;
  recurMinutesArray: Array<string>;
  recurHoursArray: Array<string>;

  constructor(
    private _schedular: SchedularService,
    private _toaster: ToastrService,
    private _fb: FormBuilder,
    private _dialog: MatDialog,
    private _spinner: SpinnerService
  ) {
    this.displayedColumns = ['srNo', 'schedulerName', 'schedulerType', 'createdAt', 'actions'];
    this.schedularForm = this._fb.group({
      scheduleName: ['', Validators.required],
      scheduleDate: ['', Validators.required],
      scheduleTime: ['', Validators.required],
      recurTimeHour: [
        '',
        this.recurType === 'Day' || this.recurType === 'Week' ? Validators.required : '',
      ],
      recurTimeMinute: [
        '',
        this.recurType === 'Day' || this.recurType === 'Week' ? Validators.required : '',
      ],
      recurValue: ['', Validators.required],
    });
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getSchdularsList();
  }

  /**
   * Opens the schedular modal
   * @param hostTemplate
   */
  openSchedularModal(schedularTemplate: TemplateRef<any>, schedularData?: any) {
    if (schedularData) {
      this.isEdit = true;
      this.schedularIdToUpdate = schedularData._id;
      this.recurTypeChange(schedularData.recurType);

      /** Prefills the schedular form to edit details */
      this.schedularForm.patchValue({
        scheduleName: schedularData.scheduleName,
        scheduleDate: schedularData.scheduleDate,
        scheduleTime: `${new Date(schedularData.scheduleTime).getHours()}:${new Date(
          schedularData.scheduleTime
        ).getMinutes()}`,
        recurTimeHour: schedularData.recurTimeHour,
        recurTimeMinute: schedularData.recurTimeMinute,
        recurValue: schedularData.recurValue,
      });
      this.recurType = schedularData.recurType;
    } else {
      this.schedularForm.reset();
      this.isEdit = false;
      this.recurType = 'Minute';
      this.recurTypeChange('Minute');
    }
    this.dialogRef = this._dialog.open(schedularTemplate, {
      minWidth: '50vw',
    });
  }

  /**
   * Handles as the recure type changes
   * @param recurType
   */
  recurTypeChange(recurType: string): void {
    // console.log('form --->', this.schedularForm);
    this.schedularForm.updateValueAndValidity();

    switch (recurType) {
      case 'Minute':
        // 1-59
        this.recurCountArray = this.generateDynamicArray(1, 59);
        this.schedularForm.get('recurTimeHour').clearValidators();
        this.schedularForm.get('recurTimeHour').updateValueAndValidity();
        this.schedularForm.get('recurTimeMinute').clearValidators();
        this.schedularForm.get('recurTimeMinute').updateValueAndValidity();
        break;

      case 'Hour':
        // 1-23
        this.recurCountArray = this.generateDynamicArray(1, 23);
        this.schedularForm.get('recurTimeHour').clearValidators();
        this.schedularForm.get('recurTimeHour').updateValueAndValidity();
        this.schedularForm.get('recurTimeMinute').clearValidators();
        this.schedularForm.get('recurTimeMinute').updateValueAndValidity();
        break;

      case 'Day':
        // 1-31
        this.recurCountArray = this.generateDynamicArray(1, 31);
        this.recurMinutesArray = this.generateDynamicArray(0, 59);
        this.recurHoursArray = this.generateDynamicArray(0, 23);
        this.schedularForm.get('recurValue').setValue(null);
        this.schedularForm.get('recurValue').setValidators(Validators.required);
        this.schedularForm.get('recurValue').updateValueAndValidity();
        break;

      case 'Week':
        // Mon,Sun,..
        this.recurCountArray = [
          'Sunday',
          'Monday',
          'Tuesday',
          'Wednesday',
          'Thursday',
          'Friday',
          'Saturday',
        ];
        this.recurMinutesArray = this.generateDynamicArray(0, 59);
        this.recurHoursArray = this.generateDynamicArray(0, 23);
        this.schedularForm.get('recurValue').setValue(null);
        this.schedularForm.get('recurValue').setValidators(Validators.required);
        this.schedularForm.get('recurValue').updateValueAndValidity();
        break;
    }
  }

  /**
   * Constructs the rule
   * @param formData schedular details
   */
  createJobRule(formData: any): string {
    let minValue: string = '*';
    let hourValue: string = '*';
    let dayValue: string = '*';
    let monthValue: string = '*';
    let weekDayValue: string = '*';
    let rule: string = '';

    if (this.recurType == 'Minute') {
      minValue = '*/' + formData.recurValue;
    } else if (this.recurType == 'Hour') {
      minValue = '0';
      hourValue = '*/' + formData.recurValue;
    } else if (this.recurType == 'Day') {
      minValue = formData.recurTimeMinute;
      hourValue = formData.recurTimeHour;
      dayValue = '*/' + formData.recurValue;
    } else if (this.recurType == 'Week') {
      minValue = formData.recurTimeMinute;
      hourValue = formData.recurTimeHour;
      weekDayValue = formData.recurValue.slice(0, 3);
    }
    rule = `${minValue} ${hourValue} ${dayValue} ${monthValue} ${weekDayValue}`;
    return rule;
  }

  /**
   * Generates the dynamic array
   * @param start
   * @param end
   */
  generateDynamicArray(start: number, end: number): Array<any> {
    let tempArray: Array<any> = [];
    for (let i = start; i <= end; i++) {
      tempArray.push(i.toString());
    }
    return tempArray;
  }

  /**
   * Creates a new schedular
   * @param formData
   */
  onSubmitSchedular(formData: any): void {
    formData.rule = this.createJobRule(formData);
    const scheduleDate = moment(formData.scheduleDate).format('YYYY-MM-DD');
    const concatValue = moment(`${scheduleDate} ${formData.scheduleTime}`);
    formData.scheduleStartTime = concatValue;
    formData.scheduleTime = concatValue;
    formData.recurType = this.recurType;
    this.isEdit ? this.updateSchedular(formData) : this.createNewSchedular(formData);
  }

  /**
   * Creates a new schedular
   * @param formData
   */
  createNewSchedular(formData: any) {
    this.showSpinner = true;
    this.schedularSub$ = this._schedular.createNewSchedular(formData).subscribe(
      (res: any) => {
        this.showSpinner = false;
        if (res) {
          this._toaster.success('Scheduler created successfully', 'Scheduler Setting');
          this.getSchdularsList();
        } else {
          this._toaster.error('Cannot create new scheduler', 'Scheduler Setting');
        }
      },
      (err: any) => {
        this.showSpinner = false;
        this._toaster.error(err.error.message || 'Server Error', 'Scheduler Setting');
      }
    );
  }

  /**
   * Updates the schedular details
   * @param formData
   */
  updateSchedular(formData: any) {
    this.showSpinner = true;
    this.schedularSub$ = this._schedular
      .updatedSchedular(formData, this.schedularIdToUpdate)
      .subscribe(
        (res: any) => {
          this.showSpinner = false;
          if (res) {
            this._toaster.success('Scheduler updated successfully', 'Scheduler Setting');
            this.getSchdularsList();
          } else {
            this._toaster.error('Cannot update scheduler', 'Scheduler Setting');
          }
        },
        (err: any) => {
          this.showSpinner = false;
          this._toaster.error(err.error.message || 'Server Error', 'Scheduler Setting');
        }
      );
  }

  /**
   * Fetches the schdedulars list
   */
  getSchdularsList(): void {
    this.schedularSub$ = this._schedular.getSchedularsList().subscribe(
      (res: any) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          this._toaster.error('Cannot fetch the schedulers list', 'Scheduler Setting');
        }
        this._spinner.stopSpinner();
      },
      (err: any) => {
        this._spinner.stopSpinner();
        this._toaster.error(err.error.message || 'Server Error', 'Scheduler Setting');
      }
    );
  }

  /**
   * Handles the schedular operation to start/stop
   * @param schedualarData
   */
  schedularHandler(schedualarData: any): void {
    if (schedualarData.isStopped) {
      schedualarData.isStopped = false;
      this.startSchedular(schedualarData._id);
    } else {
      schedualarData.isStopped = true;
      this.stopSchedular(schedualarData._id);
    }
  }

  /**
   * Starts the schedular execution
   * @param schedularId
   */
  startSchedular(schedularId: string): void {
    this.schedularSub$ = this._schedular.startSchedular(schedularId).subscribe(
      (res: any) => {
        if (res) {
          this._toaster.success('Scheduler started successfully', 'Scheduler Setting');
        } else {
          this._toaster.error('Cannot start the scheduler', 'Scheduler Setting');
        }
      },
      (err: any) => {
        // this.toggleButton = false;
        this._toaster.error(err.error.message || 'Server Error', 'Scheduler Setting');
      }
    );
  }

  /**
   * Stop the schedular execution
   * @param schedularId
   */
  stopSchedular(schedularId: string) {
    this.schedularSub$ = this._schedular.stopSchedular(schedularId).subscribe(
      (res: any) => {
        if (res) {
          this._toaster.success('Scheduler stopped successfully', 'Scheduler Setting');
        } else {
          this._toaster.error('Cannot stop the scheduler', 'Scheduler Setting');
        }
      },
      (err: any) => {
        // this.toggleButton = false;
        this._toaster.error(err.error.message || 'Server Error', 'Scheduler Setting');
      }
    );
  }

  /**
   * Deletes the schedular
   * @param schedularData
   */
  deleteSchedular(schedularData: any): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this scheduler!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        this.schedularSub$ = this._schedular.deleteSchedular(schedularData._id).subscribe(
          (res: any) => {
            if (res) {
              this.getSchdularsList();
              this._toaster.success('Scheduler deleted successfully', 'Scheduler Setting');
            } else {
              this._toaster.error('Cannot delete scheduler', 'Scheduler Setting');
            }
          },
          (err: any) => {
            this._toaster.error(err.error.message || 'Server Error', 'Scheduler Setting');
          }
        );
      }
    });
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.schedularSub$) {
      this.schedularSub$.unsubscribe();
    }
  }
}
