import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PscoreNodesService {

  serverBaseUrl = environment.serverBaseUrl;

  constructor(private _http: HttpClient) { }
 
  /**
   * Fetches the pscore nodes list
   */
  getPscoreNodeList(): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/settings/pscoreIdCreation`);
  }

  /**
   * Creates a new node into database
   * @param nodeData
   */
  createNewNode(nodeData: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/settings/pscoreIdCreation`, nodeData);
  }

  /**
   * Deletes the node from DB
   * @param nodeData 
   */
  deleteNode(nodeId: string): Observable<any> {
    return this._http.delete(`${this.serverBaseUrl}/api/settings/pscoreIdCreation/${nodeId}`);
  }

}
