import { ToastrService } from 'ngx-toastr';
import { CreateConnectionService } from '../create-connection.service';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CreateConnectionComponent } from '../create-connection.component';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { ThemePalette } from '@angular/material/core';
import { Subscription } from 'rxjs';

const swal = require('sweetalert');
@Component({
  selector: 'app-connection-form',
  templateUrl: './connection-form.component.html',
  styleUrls: ['./connection-form.component.scss'],
})
export class ConnectionFormComponent implements OnInit {
  /** Different database types */
  databaseTypes = [
    {
      name: 'MSSQL',
      value: 'mssql',
      type: 'sql',
    },
    {
      name: 'MySQL',
      value: 'mysql',
      type: 'sql',
    },
    {
      name: 'PostgreSQL',
      value: 'postgresql',
      type: 'sql',
    },
  ];

  /** Progress spinner settings */
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;

  /** Subscription variables */
  connection$: Subscription;

  connectionForm: FormGroup;
  showSpinner: Boolean = false;
  isEdit: Boolean = false;
  connectionIdToUpdate: string;
  // pattern = ^(?:[0-9]{1,3}.){3}[0-9]{1,3}$;

  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    _fb: FormBuilder,
    private _connection: CreateConnectionService,
    private _toaster: ToastrService,
    private dialogRef: MatDialogRef<CreateConnectionComponent>
  ) {
    this.connectionForm = _fb.group({
      name: ['', [Validators.required, Validators.pattern('^[^\\s][A-Za-z0-9_\\- ]+$')]],
      source: ['', dialogData.connectionType === 'db' ? Validators.required : ''],
      address: ['', [Validators.required]],
      port: ['', [Validators.required]],
      username: ['', [Validators.required]],
      password: ['', Validators.required],
      //predictsensePwd: ['', Validators.required],
    });
    this.prefillConnectionForm(this.dialogData.connectionData);
  }

  /**
   * Initializes the page with required operations
   */
  ngOnInit(): void { }

  /**
   * Prefill the connection form for edit functionality
   * @param connectionData selected connectio details
   */
  prefillConnectionForm(connectionData: any): void {
    if (connectionData) {
      this.isEdit = true;
      this.connectionIdToUpdate = connectionData._id;
      this.connectionForm.patchValue({
        name: connectionData.name,
        source: connectionData.source,
        address: connectionData.address,
        port: connectionData.port,
        username: connectionData.username,
      });
    } else {
      this.isEdit = false;
      this.connectionForm.reset();
      //this.connectionForm.get('source').setValue('mssql');
    }
  }

  /**
   * Test the connection if it is connected successfully
   * @param connectionDetails
   */
  testConnectionHandler(connectionDetails: any) {
    if (this.dialogData.connectionType === 'db') {
      this.testDBConnection(connectionDetails);
    } else {
      this.testSFTPConnection(connectionDetails);
    }
  }

  /**
   * Test the DB connection
   * @param connectionDetails
   */
  testDBConnection(connectionDetails: any) {
    this.showSpinner = true;
    this.connection$ = this._connection.testDBConnection(connectionDetails).subscribe(
      (res) => {
        this.showSpinner = false;
        if (res) {
          this.showAlert(res.message, res.status);
        } else {
          this.showAlert(res.message, res.status);
        }
      },
      (err) => {
        this.showSpinner = false;
        this._toaster.error('Server Error', 'Database Settings');
      }
    );
  }

  /**
   * Test the SFTP connection
   * @param connectionDetails
   */
  testSFTPConnection(connectionDetails: any) {
    this.showSpinner = true;
    this.connection$ = this._connection.testSFTPConnection(connectionDetails).subscribe(
      (res) => {
        this.showSpinner = false;
        if (res) {
          this.showAlert(res.message, res.status);
        } else {
          this.showAlert(res.message, res.status);
        }
      },
      (err) => {
        this.showSpinner = false;
        this._toaster.error('Server Error', 'SFTP Settings');
      }
    );
  }

  /**
   * Renders the alert notification pop-up
   * @param msg connection status message
   * @param status connection status
   */
  showAlert(msg: string, status: string): void {
    swal({
      title: 'Connection Status',
      text: msg,
      icon: status === 'success' ? 'success' : 'info',
      className: 'custom-alert',
    });
  }

  /**
   * Handles the connection type whether DB or SFTP connection
   * @param formValue connection details
   */
  onSubmitConnection(formValue: any): void {
    if (this.isEdit) {
      this.updateConnectionHandler(formValue);
    } else {
      this.createConnectionHandler(formValue);
    }
  }

  updateConnectionHandler(formValue: any) {
    if (this.dialogData.connectionType === 'db') {
      this.updateDBConnection(formValue);
    } else {
      this.updateSFTPConnection(formValue);
    }
  }

  /**
   * Handles creating new connection as per connection type
   * @param formValue connection details
   */
  createConnectionHandler(formValue: any) {
    if (this.dialogData.connectionType === 'db') {
      this.createDBConnection(formValue);
    } else {
      this.createSFTPConnection(formValue);
    }
  }

  /**
   * Updates the selected DB connection details
   * @param connectionDetails
   */
  updateDBConnection(connectionDetails: any): void {
    this.connection$ = this._connection
      .updateDBConnection(connectionDetails, this.connectionIdToUpdate)
      .subscribe(
        (res) => {
          if (res) {
            this.dialogRef.close(JSON.stringify(res));
            this._toaster.success('Database connection updated successfully', 'Database Settings');
          } else {
            this._toaster.error('Cannot updated database connection', 'Database Settings');
          }
        },
        (err) => {
          this._toaster.error('Server Error', 'Database Settings');
        }
      );
  }

  /**
   * Updates the SFTP connection details
   * @param connectionDetails
   */
  updateSFTPConnection(connectionDetails: any): void {
    this.connection$ = this._connection
      .updateSFTPConnection(connectionDetails, this.connectionIdToUpdate)
      .subscribe(
        (res) => {
          if (res) {
            this.dialogRef.close(JSON.stringify(res));
            this._toaster.success('SFTP connection updated successfully', 'SFTP Settings');
          } else {
            this._toaster.error('Cannot updated SFTP connection', 'SFTP Settings');
          }
        },
        (err) => {
          this._toaster.error('Server Error', 'SFTP Settings');
        }
      );
  }

  /**
   * Creates a new database connections
   * @param connectionDetails
   */
  createDBConnection(connectionDetails: any): void {
    this.connection$ = this._connection.createDBConnection(connectionDetails).subscribe(
      (res) => {
        if (res) {
          this.dialogRef.close(JSON.stringify(res));
          this._toaster.success('Database connection created successfully', 'Database Settings');
        } else {
          this._toaster.error('Cannot create database connection', 'Database Settings');
        }
      },
      (err) => {
        this._toaster.error('Server Error', 'Database Settings');
      }
    );
  }

  /**
   * Creates a new SFTP connections
   * @param connectionDetails
   */
  createSFTPConnection(connectionDetails: any): void {
    delete connectionDetails.source;
    this.connection$ = this._connection.createSFTPConnection(connectionDetails).subscribe(
      (res) => {
        if (res) {
          this.dialogRef.close(JSON.stringify(res));
          this._toaster.success('SFTP connection created successfully', 'SFTP Settings');
        } else {
          this._toaster.error('Cannot create SFTP connection', 'SFTP Settings');
        }
      },
      (err) => {
        this._toaster.error('Server Error', 'SFTP Settings');
      }
    );
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.connection$) {
      this.connection$.unsubscribe();
    }
  }
}
