import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
@Injectable({
  providedIn: 'root'
})
export class ManagePlansService {
  serverBaseUrl: string = environment.serverBaseUrl;
  formData: FormData = new FormData();
  constructor(private _http: HttpClient) { }

  /**
   * Fetches the plans data from DB
   */
  getPlansData(): Observable<any> {
      return this._http.get(`${this.serverBaseUrl}/api/v2/manage/plans/`);
  }

  /**
   * Push the new rules to plan collection.
   */
  addNewRule(ruleData: any, submitType: string, planType?: string,): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/manage/plans?reqType=${submitType}`, {ruleData, planType});
  }

  /**
   * Push the new rules to plan collection.
   */
   updateAndSaveRule(ruleData: any, planType?: string,): Observable<any> {
    return this._http.put(`${this.serverBaseUrl}/api/v2/manage/plans/`, {ruleData, planType});
  }

}
