import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-project-handler',
  templateUrl: './project-handler.component.html',
  styleUrls: ['./project-handler.component.scss'],
})
export class ProjectHandlerComponent implements OnInit {
  @Output() goBackFlag = new EventEmitter<boolean>();
  @Output() importProject = new EventEmitter<boolean>();
  @Input() projectData: any;
  @Input() type: any;
  dialogHeading: string;

  constructor() {}

  ngOnInit(): void {
    switch (this.type) {
      case 'projectDetails':
        this.dialogHeading = this.projectData.name;
        break;
    }
  }
}
