import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { DomainList } from 'src/app/shared/static-data';
import { SampleTemplatesService } from './sample-templates.service';
import { TemplateFormComponent } from './template-form/template-form.component';
const swal = require('sweetalert');
@Component({
  selector: 'app-sample-templates',
  templateUrl: './sample-templates.component.html',
  styleUrls: ['./sample-templates.component.scss'],
})
export class SampleTemplatesComponent implements OnInit {
  /** mat-table settings */
  dataSource: MatTableDataSource<[]>;
  @ViewChild('templatePaginator') templatePaginator: MatPaginator;
  @ViewChild('templateSort') templateSort: MatSort;

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  pageSizeOptions: Array<number> = [3, 5, 10, 15, 20];
  displayedColumns: Array<string> = [];
  allTemplateList: Array<any>;

  constructor(
    public _dialog: MatDialog,
    private _template: SampleTemplatesService,
    private _toaster: ToastrService,
    private _spinner: SpinnerService
  ) {
    this.displayedColumns = ['srNo', 'name', 'domain', 'createdAt', 'updatedAt', 'actions'];
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getTemplatesList();
  }

  /**
   * Fetches the templates list from DB
   */
  getTemplatesList() {
    // API call
    this._template
      .getAllTemplates()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._spinner.stopSpinner();
            this.allTemplateList = res.data;
            // Assign the data to the data source for the table to render
            this.dataSource = new MatTableDataSource(res.data);
            this.dataSource.paginator = this.templatePaginator;
            this.dataSource.sort = this.templateSort;
          } else {
            this._spinner.stopSpinner();
            this._toaster.error('Cannot fetch sample project list', 'Sample Projects');
          }
        },
        (err: HttpErrorResponse) => {
          this._spinner.stopSpinner();
          this._toaster.error(err.error.message || 'Server Error', 'Sample Projects');
        }
      );
  }

  /**
   * Deletes the selected template
   * @param templateData
   */
  deleteTemplate(templateData: any): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this sample projects.',
      icon: 'warning',
      dangerMode: true,
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
          closeModal: true,
        },
        confirm: {
          text: 'Delete',
          visible: true,
          className: 'bg-danger',
          closeModal: true,
        },
      },
    }).then((isConfirm: any) => {
      if (isConfirm) {
        this._template
          .deleteTemplate(templateData._id)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe(
            (res: any) => {
              if (res) {
                // this.getUsersList();
                this._toaster.success('Sample project deleted successfully', 'Sample Projects');
                this.dataSource.data = this.dataSource.data.filter(
                  (i: any) => i._id !== templateData._id
                );
                this.allTemplateList = this.allTemplateList.filter(
                  (i: any) => i._id !== templateData._id
                );
                this.dataSource._updateChangeSubscription();
              } else {
                this._toaster.error('Cannot delete the sample project', 'Sample Projects');
              }
            },
            (err: HttpErrorResponse) => {
              this._toaster.error(err.error.message || 'Server Error', 'Sample Projects');
            }
          );
      }
    });
  }

  /**
   * Opens the create new template form dialog
   * @param userData
   */
  openCreateTemplateModal(templateData?: any): void {
    const dialogRef = this._dialog.open(TemplateFormComponent, {
      data: {
        templateData: templateData || null,
        allTemplateList: this.allTemplateList,
      },
    });
    dialogRef.afterClosed().subscribe((tempData) => {
      if (tempData && tempData.data) {
        if (tempData.isEdit) {
          this.dataSource.data = this.dataSource.data.map((i: any) => {
            i._id === tempData.data._id ? (i = tempData.data) : null;
            return i;
          });
          this.allTemplateList = this.allTemplateList.map((i: any) => {
            i._id === tempData.data._id ? (i = tempData.data) : null;
            return i;
          });
        } else {
          this.dataSource.data.push(tempData.data);
        }
        this.dataSource._updateChangeSubscription();
      }
    });
  }

  /**
   * Returns the project domain name
   * @param data sample project details
   */
  getProjectDomain = (data: any) =>
    DomainList.find((i: any) => i.value === data.domain)?.name || '-';

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
