import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
@Injectable({
  providedIn: 'root'
})
export class UploadModelService {
  serverBaseUrl: string = environment.serverBaseUrl;
  formData: FormData = new FormData();
  constructor(private _http: HttpClient) { }

  /**
   * Fetches the model list from DB
   */
  getTaModelList(): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/v2/upload/model`);
  }

  /**
     * Creates a new model
     * @param modelDetails 
     */
  uploadTaModel(reqObj: any): Observable<any> {
    this.formData.set('file', reqObj.file);
    if (reqObj.file) {
      if (reqObj.selectedTaskType) {
        return this._http.post(
          `${this.serverBaseUrl}/api/v2/upload/model?frameWork=${reqObj.selectedFrameWork}&taskType=${reqObj.selectedTaskType}`,
          this.formData, { responseType: 'text' }
        );
      } else {
        return this._http.post(
          `${this.serverBaseUrl}/api/v2/upload/model?frameWork=${reqObj.selectedFrameWork}`,
          this.formData, { responseType: 'text' }
        );
      }
    } else {
      return this._http.post(
        `${this.serverBaseUrl}/api/v2/upload/model`,
        reqObj, { responseType: 'text' }
      );
    }

  }

  /**
     * Deletes the usecase from DB
     * @param modelId 
     */
  deleteTaModel(modelId: string): Observable<any> {
    return this._http.delete(`${this.serverBaseUrl}/api/v2/upload/model?modelId=${modelId}`);
  }

}
