import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class LogsService {

  serverBaseUrl: string = environment.serverBaseUrl;

  constructor(private _http: HttpClient) { }

  /**
   * Exports the PSSTUDIO logs
   * @param logDetails 
   */
  exportPsstudioLogs(logDetails: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/export-logs`, logDetails);
  }

  /**
   * Exports the operating system logs
   * @param logDetails 
   */
  exportOSLogs(logDetails: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/export-os-logs`, logDetails);
  }

  /**
   * Exports the PSCORE logs
   * @param pscoreConfigId 
   */
  exportPscoreLogs(pscoreConfigId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/export-pscore-app-logs/${pscoreConfigId}`);
  }
}
