import { HttpErrorResponse } from '@angular/common/http';
import { SnowflakeAndS3FormComponent } from './snowflake-and-s3-form/snowflake-and-s3-form.component';
import { MatDialogRef } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute } from '@angular/router';
import { CreateConnectionService } from './create-connection.service';
import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { MatDialog } from '@angular/material/dialog';
import { ConnectionFormComponent } from './connection-form/connection-form.component';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { TitleCasePipe } from '@angular/common';
import { LocalStorageService } from 'src/app/shared/local-storage.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-create-connection',
  templateUrl: './create-connection.component.html',
  styleUrls: ['./create-connection.component.scss'],
})
export class CreateConnectionComponent implements OnInit, OnDestroy {
  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  dataSource: MatTableDataSource<[]>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  /** Subscription variables */
  connectionSub$: Subscription;

  dialogRef: MatDialogRef<any>;
  pageSizeOptions: Array<number> = [3, 5, 10, 15, 20];
  displayedColumns: Array<string> = [];

  connectionType: string;
  getQueryParams: any;
  user: any;

  constructor(
    private _activatedRoute: ActivatedRoute,
    public _dialog: MatDialog,
    private _connection: CreateConnectionService,
    private _toaster: ToastrService,
    private _spinner: SpinnerService,
    private _titlecase: TitleCasePipe,
    private _localStorage: LocalStorageService
  ) {
    if (this._activatedRoute.queryParams) {
      this._activatedRoute.queryParams.subscribe((val) => {
        this.connectionType = val.type;
        this.getAllData();
      });
    }
  }

  ngOnInit(): void {
    this.user = this._localStorage.getItem('user');
  }

  /**
   * Fetches all the required data as per connection type
   */
  getAllData(): void {
    switch (this.connectionType) {
      case 'db':
        this.displayedColumns = ['srNo', 'name', 'database', 'createdAt', 'actions'];
        this.getDbConnectionsList();
        break;
      case 'sftp':
        this.displayedColumns = ['srNo', 'name', 'type', 'createdAt', 'actions'];
        this.getSftpConnectionList();
        break;
      case 'snowflake':
        this.displayedColumns = ['srNo', 'name', 'sfAccount', 'username', 'createdAt', 'actions'];
        this.getRequiredConnectionList();
        break;
      case 's3':
        this.displayedColumns = ['srNo', 'name', 'awsRegion', 'createdAt', 'actions'];
        this.getRequiredConnectionList();
        break;
      case 'bigquery':
        this.displayedColumns = ['srNo', 'name', 'bqProjectId', 'createdAt', 'actions'];
        this.getRequiredConnectionList();
        break;
    }
  }

  /**
   * Fetches all database connections list
   */
  getDbConnectionsList(): void {
    this.connectionSub$ = this._connection.getDbConnectionList().subscribe((res) => {
      if (res) {
        // Assign the data to the data source for the table to render
        this.dataSource = new MatTableDataSource(res);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }
    });
    this._spinner.stopSpinner();
  }

  /**
   * Fetches all snowflake/s3 connections list
   */
  getRequiredConnectionList(): void {
    this._connection
      .getConnectionsList(this.connectionType)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(async (res) => {
        if (res) {
          // Assign the data to the data source for the table to render
          this.dataSource = new MatTableDataSource(res);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        }
      });
    this._spinner.stopSpinner();
  }

  /**
   * Fetches all SFTP connections list
   */
  getSftpConnectionList(): void {
    this.connectionSub$ = this._connection.getSftpConnectionList().subscribe(async (res) => {
      if (res) {
        // Assign the data to the data source for the table to render
        this.dataSource = new MatTableDataSource(res);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }
    });
    this._spinner.stopSpinner();
  }

  /**
   * Handles the delete connection operation as per connection type - db/sftp
   * @param data selected connection details
   */
  deleteConnectionHandler(data: any): void {
    switch (this.connectionType) {
      case 'db':
        this.deleteDBConnection(data);
        break;
      case 'sftp':
        this.deleteSFTPConnection(data);
        break;
      case 'snowflake':
      case 's3':
      case 'bigquery':
        this.deleteConnection(data);
        break;
    }
  }

  /**
   * Deletes the snowflake/s3 connection
   * @param data connection to be deleted
   */
  deleteConnection(data: any): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this connection.',
      icon: 'warning',
      dangerMode: true,
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
          closeModal: true,
        },
        confirm: {
          text: 'Delete',
          visible: true,
          className: 'bg-danger',
          closeModal: true,
        },
      },
    }).then((isConfirm: any) => {
      if (isConfirm) {
        this._spinner.showSpinner();
        this._connection
          .deleteConnection(data._id, this.connectionType)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe(
            (res) => {
              if (res) {
                this._spinner.stopSpinner();
                this.getAllData();
                this._toaster.success(
                  `${this._titlecase.transform(res.name)} connection deleted successfully`,
                  `${this._titlecase.transform(this.connectionType)} Settings`
                );
              } else {
                this._spinner.stopSpinner();
                this._toaster.error(
                  `Cannot delete ${res.name} connection`,
                  `${this._titlecase.transform(this.connectionType)} Settings`
                );
              }
            },
            (err: HttpErrorResponse) => {
              this._spinner.stopSpinner();
              this._toaster.error(
                err.error.message || 'Server Error',
                `${this._titlecase.transform(this.connectionType)} Settings`
              );
            }
          );
      }
    });
  }

  /**
   * Deletes the DB connection
   * @param data connection to be deleted
   */
  deleteDBConnection(data: any) {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this connection.',
      icon: 'warning',
      dangerMode: true,
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
          closeModal: true,
        },
        confirm: {
          text: 'Delete',
          visible: true,
          className: 'bg-danger',
          closeModal: true,
        },
      },
    }).then((isConfirm: any) => {
      if (isConfirm) {
        this._spinner.showSpinner();
        this._connection
          .deleteDBConnection(data._id)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe(
            (res) => {
              if (res) {
                this.getAllData();
                this._toaster.success(
                  `${this._titlecase.transform(data.name)} connection deleted successfully`,
                  'Database Settings'
                );
              } else {
                this._spinner.stopSpinner();
                this._toaster.error(`Cannot delete ${data.name} connection`, 'Database Settings');
              }
            },
            (err) => {
              this._spinner.stopSpinner();
              this._toaster.error(err.error.message || 'Server Error', 'Database Settings');
            }
          );
      }
    });
  }

  /**
   * Deletes the SFTP connection
   * @param data connection to be deleted
   */
  deleteSFTPConnection(data: any) {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this connection.',
      icon: 'warning',
      dangerMode: true,
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
          closeModal: true,
        },
        confirm: {
          text: 'Delete',
          visible: true,
          className: 'bg-danger',
          closeModal: true,
        },
      },
    }).then((isConfirm: any) => {
      if (isConfirm) {
        this._spinner.showSpinner();
        this._connection
          .deleteSFTPConnection(data._id)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe(
            (res) => {
              if (res) {
                this.getAllData();
                this._toaster.success(
                  `${this._titlecase.transform(data.name)} connection deleted successfully`,
                  'SFTP Settings'
                );
              } else {
                this._spinner.stopSpinner();
                this._toaster.error(`Cannot delete ${data.name} connection`, 'SFTP Settings');
              }
            },
            (err) => {
              this._spinner.stopSpinner();
              this._toaster.error(err.error.message || 'Server Error', 'SFTP Settings');
            }
          );
      }
    });
  }

  /**
   * Handles the connection form as per the type
   * @param connectionData
   */
  formDialogHandler(connectionData?: any) {
    switch (this.connectionType) {
      case 'db':
      case 'sftp':
        this.openFormDialog(ConnectionFormComponent, connectionData);
        break;
      case 'snowflake':
      case 's3':
      case 'bigquery':
        this.openFormDialog(SnowflakeAndS3FormComponent, connectionData);
        break;
    }
  }

  /**
   * Opens the connection form dialog
   */
  openFormDialog(tempRef: any, connectionData: any): void {
    this.dialogRef = this._dialog.open(tempRef, {
      minWidth: '60vw',
      data: {
        connectionType: this.connectionType,
        connectionData: connectionData,
      },
    });
    this.dialogRef.afterClosed().subscribe((connectionDetails) => {
      this.getAllData();
    });
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.connectionSub$) {
      this.connectionSub$.unsubscribe();
    }
  }
}
