import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit, TemplateRef } from '@angular/core';
import { FormArray, FormBuilder, FormControl, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { ManagePlansService } from './manage-plans.service';

@Component({
  selector: 'app-manage-plans',
  templateUrl: './manage-plans.component.html',
  styleUrls: ['./manage-plans.component.scss']
})
export class ManagePlansComponent implements OnInit {

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();
  planTypeList: Array<string> = [];
  restrictionsList: Array<any> = null;
  user: any;
  dialogRef: any;
  ruleTypeList: Array<any> = ["number", "dropdown"];
  addNewRuleForm: any;
  addNewPlanForm: any;
  newRuleArray: Array<any> = [];
  allowEdit: Boolean = false;
  requestingCardName: string = null;

  constructor(
    private _managePlansService: ManagePlansService,
    private _spinner: SpinnerService,
    private _toaster: ToastrService,
    private _localStorage: LocalStorageService,
    private _dialog: MatDialog,
    private _fb: FormBuilder
  ) {
    this.addNewRuleForm = this._fb.group({
      moduleName: ['', Validators.required],
      selectedPlanType: ['', Validators.required],
      selectedRuleType: ['', Validators.required],
      ruleText: ['', Validators.required],
      ruleValue: ['', Validators.required],
      allowedRuleValue: ['', Validators.required]
    });
    this.addNewPlanForm = this._fb.group({
      planName: ['', Validators.required],
      selectedPlanTemplate: ['', Validators.required],
    });
  }

  ngOnInit(): void {
    this.user = this._localStorage.getItem('user');
    this.getPlanDetailsForManagePlans('basic');
  }

  /**
   * function to get the plan detals for manage plan option in settings.
   * only accesible by sudo.
   */
  getPlanDetailsForManagePlans(planType: string): void {
    this._spinner.showSpinner();
    this._managePlansService
      .getPlansData()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: Array<any>) => {
          if (res) {
            this.planTypeList = res.map(item => item.planType);
            this.restrictionsList = res.map(val => {
              if (val.planType === planType) {
                return val.restrictionPlans;
              }
            });
            this.restrictionsList = this.restrictionsList.filter(val => val !== undefined)[0];
            this._spinner.stopSpinner();
            // console.log('this si res : ', res, this.planTypeList, this.restrictionsList);
          } else {
            this._spinner.stopSpinner();
            this._toaster.info('Cannot fetch plans data');
          }
        },
        (err: HttpErrorResponse) => {
          this._spinner.stopSpinner();
          this._toaster.info('Cannot fetch plans data');
        }
      );
  }

  /**
   * function to edit the restriction rules.
   * @param restriction 
   * @param radioGroupVal 
   * @param inputVal 
   */
  editAndSaveRestrictions(requestType: string, restrictionRules?: any, planType?: string) {
    this._spinner.showSpinner();
    // console.log('thi sis the data before update : ', restrictionRules);
    if (requestType === 'updateRequest') {
      // console.log('this is inside updaterequest : ', restrictionRules);
      this._managePlansService
        .updateAndSaveRule(restrictionRules, planType)
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(
          (res: any) => {
            if (res) {
              // console.log('this is success res', res);
              this._toaster.success(res.message || 'Rules updated successfully', 'Manage Plan');
            }
            this.allowEdit = !this.allowEdit;
            this._spinner.stopSpinner();
            this.getPlanDetailsForManagePlans(planType);
          },
          (err: HttpErrorResponse) => {
            // console.log('this is the err : ', err);
            this._spinner.stopSpinner();
            this._toaster.info('Cannot add new rule');
          }
        );
    } else if (requestType === 'manageRequest') {
      this.requestingCardName = restrictionRules.moduleName;
      this.allowEdit = !this.allowEdit;
      // console.log('this is manage request : ', this.requestingCardName, this.allowEdit);
      this._spinner.stopSpinner();
    }
    // console.log('thi sis the data end of function : ', restrictionRules);
  }

  /**
   * Opens the manage plans dialog box
   * @param managePlansType
   */
  openManagePlansDialogBox(managePlansType: TemplateRef<any>) {
    this.dialogRef = this._dialog.open(managePlansType, {
      minWidth: '50vw',
    });
  }

  /**
   * function to submit the response from the user and add the new rule to plans.
   */
  onSubmit(formData: any, submitType: string) {
    this._spinner.showSpinner();
    if (submitType === 'createRule') {
      let tempRule = {
        label: formData.ruleText + " : ",
        type: formData.selectedRuleType,
        name: String(formData.ruleText).replace(/\s/g, '').toLowerCase(),
        values: String(formData.ruleValue).split(','),
        allowedValues: String(formData.allowedRuleValue).split(',')
      }
      this.newRuleArray.push(tempRule)
      this.addNewRuleForm.reset();
      // creating the object to store it in the db.
      let tempRestrictionData = {
        moduleName: formData.moduleName,
        rules: this.newRuleArray,
      }
      // console.log('this si the user res  : ', formData, this.newRuleArray, tempRestrictionData);
      this._managePlansService
        .addNewRule(tempRestrictionData, submitType, formData.selectedPlanType)
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(
          (res: any) => {
            if (res) {
              // console.log('this is success res', res);
              this._spinner.stopSpinner();
              this._toaster.success(res.message || 'New rules added successfully', 'Manage Plan');
            } else {
              this._spinner.stopSpinner();
              this._toaster.info('Cannot add new rule');
            }
            this.newRuleArray.length = 0;
            this.getPlanDetailsForManagePlans(formData.selectedPlanType);
          },
          (err: HttpErrorResponse) => {
            // console.log('this is the err : ', err);
            this.newRuleArray.length = 0;
            this._spinner.stopSpinner();
            this._toaster.info('Cannot add new rule');
          }
        );
    } else if (submitType === 'createPlan') {
      // console.log('this is submit type : ', submitType, formData);
      this._managePlansService
        .addNewRule(formData, submitType)
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(
          (res: any) => {
            if (res) {
              // console.log('this is success res', res);
              this._spinner.stopSpinner();
              this._toaster.success(res.message || 'New plan created successfully', 'Manage Plan');
            } else {
              this._spinner.stopSpinner();
              this._toaster.info('Cannot add new rule');
            }
            this.addNewPlanForm.reset();
            this.newRuleArray.length = 0;
            this.getPlanDetailsForManagePlans(formData.planName);
          },
          (err: HttpErrorResponse) => {
            // console.log('this is the err : ', err);
            this.newRuleArray.length = 0;
            this._spinner.stopSpinner();
            this._toaster.info('Cannot add new rule');
          }
        );
    }
  }

  /**
   * function for retaining the old values and disabling the form fields when cancel btn is clicked.
   */
  onCancel(planType: string) {
    this.allowEdit = !this.allowEdit
    // call the get plans api.
    this.getPlanDetailsForManagePlans(planType);
  }
  
  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }

}