import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs-compat';
import { takeUntil } from 'rxjs/operators';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { CreateConnectionService } from '../create-connection.service';

const swal = require('sweetalert');
@Component({
  selector: 'app-snowflake-and-s3-form',
  templateUrl: './snowflake-and-s3-form.component.html',
  styleUrls: ['./snowflake-and-s3-form.component.scss'],
})
export class SnowflakeAndS3FormComponent implements OnInit {
  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  snowflakeForm: FormGroup;
  s3Form: FormGroup;
  bigQueryForm: FormGroup;
  showSpinner: Boolean = false;
  isEdit: Boolean = false;
  hidePassword: boolean = true;
  hideSfPassword: boolean = true;
  connectionIdToUpdate: string;

  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    private _fb: FormBuilder,
    private _connection: CreateConnectionService,
    private _toaster: ToastrService,
    private _spinner: SpinnerService,
    private _dialogRef: MatDialogRef<SnowflakeAndS3FormComponent>,
    private _titlecase: TitleCasePipe
  ) {
    this.createFormsInstance();
    this.prefillConnectionForm(this.dialogData.connectionData);
  }

  ngOnInit(): void {}

  /**
   * Prefill the connection form for edit functionality
   * @param connectionData selected connection details
   */
  prefillConnectionForm(connectionData: any): void {
    if (connectionData) {
      this.isEdit = true;
      this.connectionIdToUpdate = connectionData._id;
      switch (this.dialogData.connectionType) {
        case 'snowflake':
          this.snowflakeForm.patchValue({
            name: connectionData.name,
            sfAccount: connectionData.sfAccount,
            username: connectionData.username,
          });
          break;
        case 's3':
          this.s3Form.patchValue({
            name: connectionData.name,
            awsRegion: connectionData.awsRegion,
            awsKeyId: connectionData.awsKeyId,
          });
          break;
        case 'bigquery':
          this.bigQueryForm.patchValue({
            name: connectionData.name,
            bqProjectId: connectionData.bqProjectId,
            bqClientEmail: connectionData.bqClientEmail,
          });
          break;
      }
    } else {
      this.isEdit = false;
      this.bigQueryForm.reset({ onlySelf: true });
    }
  }

  /**
   * Test the connection if it is connected successfully
   */
  testConnection(): void {
    this._spinner.showSpinner();
    let connDetails: any;
    if (this.dialogData.connectionType === 'snowflake') {
      connDetails = this.snowflakeForm.value;
      connDetails.source = 'snowflake';
    } else if (this.dialogData.connectionType === 's3') {
      connDetails = this.s3Form.value;
      connDetails.source = 's3';
    } else {
      connDetails = this.bigQueryForm.value;
      connDetails.source = 'bigquery';
    }
    // API
    this._connection
      .testConnection(connDetails, this.dialogData.connectionType)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          this._spinner.stopSpinner();
          if (res) {
            this.showAlert(res.message, res.status);
          } else {
            this.showAlert(res.message, res.status);
          }
        },
        (err) => {
          this._spinner.stopSpinner();
          this._toaster.error(
            err.error.message || 'Server Error',
            `${this.dialogData.connectionType} Settings`
          );
        }
      );
  }

  /**
   * Handles the connection type whether snowflake or S3 connection
   */
  onSubmitConnection(): void {
    this._spinner.showSpinner();
    if (this.isEdit) {
      this.updateConnection();
    } else {
      this.createConnection();
    }
  }

  /**
   * Updates the selected connection details
   */
  updateConnection(): void {
    let reqObj: any;
    if (this.dialogData.connectionType === 'snowflake') {
      reqObj = this.snowflakeForm.value;
      reqObj.source = 'snowflake';
    } else if (this.dialogData.connectionType === 's3') {
      reqObj = this.s3Form.value;
      reqObj.source = 's3';
    } else {
      reqObj = this.bigQueryForm.value;
      reqObj.source = 'bigquery';
    }
    // API
    this._connection
      .updateConnection(this.connectionIdToUpdate, this.dialogData.connectionType, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          if (res) {
            this._spinner.stopSpinner();
            this._dialogRef.close(JSON.stringify(res));
            this._toaster.success(
              `${this._titlecase.transform(
                this.dialogData.connectionType
              )} connection updated successfully`,
              `${this._titlecase.transform(this.dialogData.connectionType)} Settings`
            );
          } else {
            this._spinner.stopSpinner();
            this._toaster.error(
              `Cannot update ${this._titlecase.transform(
                this.dialogData.connectionType
              )} connection`,
              `${this._titlecase.transform(this.dialogData.connectionType)} Settings`
            );
          }
        },
        (err) => {
          this._spinner.stopSpinner();
          this._toaster.error(
            err.error.message || 'Server Error',
            `${this._titlecase.transform(this.dialogData.connectionType)} Settings`
          );
        }
      );
  }

  /**
   * Creates a new snowflake connections
   */
  createConnection(): void {
    let reqObj: any;
    if (this.dialogData.connectionType === 'snowflake') {
      reqObj = this.snowflakeForm.value;
      reqObj.source = 'snowflake';
    } else if (this.dialogData.connectionType === 's3') {
      reqObj = this.s3Form.value;
      reqObj.source = 's3';
    } else {
      reqObj = this.bigQueryForm.value;
      reqObj.source = 'bigquery';
    }
    // API
    this._connection
      .createNewConnection(this.dialogData.connectionType, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          if (res) {
            this._spinner.stopSpinner();
            this._dialogRef.close(JSON.stringify(res));
            this._toaster.success(
              `${this._titlecase.transform(
                this.dialogData.connectionType
              )} connection created successfully`,
              `${this._titlecase.transform(this.dialogData.connectionType)} Settings`
            );
          } else {
            this._spinner.stopSpinner();
            this._toaster.error(
              `Cannot create ${this._titlecase.transform(
                this.dialogData.connectionType
              )} connection`,
              `${this._titlecase.transform(this.dialogData.connectionType)} Settings`
            );
          }
        },
        (err) => {
          this._spinner.stopSpinner();
          this._toaster.error(
            err.error.message || 'Server Error',
            `${this._titlecase.transform(this.dialogData.connectionType)} Settings`
          );
        }
      );
  }

  /**
   *
   * @returns Returns the boolean value as per the form validation
   */
  disableButton(): boolean {
    switch (this.dialogData.connectionType) {
      case 'snowflake':
        return this.snowflakeForm.invalid;

      case 's3':
        return this.s3Form.invalid;

      case 'bigquery':
        return this.bigQueryForm.invalid;
    }
  }

  /**
   * Creates the form instances
   */
  createFormsInstance(): void {
    this.snowflakeForm = this._fb.group({
      name: ['', [Validators.required, Validators.pattern('^[^\\s][A-Za-z0-9 ]+$')]],
      sfAccount: ['', [Validators.required]],
      username: ['', [Validators.required]],
      password: ['', Validators.required],
     // predictsensePwd: ['', Validators.required],
    });
    this.s3Form = this._fb.group({
      name: ['', [Validators.required, Validators.pattern('^[^\\s][A-Za-z0-9 ]+$')]],
      awsRegion: ['', [Validators.required]],
      awsKeyId: ['', [Validators.required]],
      awsSecretKey: ['', Validators.required],
      //predictsensePwd: ['', Validators.required],
    });
    this.bigQueryForm = this._fb.group({
      name: ['', [Validators.required, Validators.pattern('^[^\\s][A-Za-z0-9 ]+$')]],
      //predictsensePwd: ['', Validators.required],
      bqProjectId: ['', [Validators.required]],
      bqPrivateKey: ['', Validators.required],
      bqClientEmail: ['', [Validators.required, Validators.email]],
    });
  }

  /**
   * Renders the alert notification pop-up
   * @param msg connection status message
   * @param status connection status
   */
  showAlert(msg: string, status: string): void {
    swal({
      title: 'Connection Status',
      text: msg,
      icon: status === 'success' ? 'success' : 'info',
      className: 'custom-alert',
    });
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
