import { SpinnerService } from 'src/app/shared/spinner.service';
import { HttpErrorResponse } from '@angular/common/http';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { ModelDeploymentService } from './model-deployment.service';
import { Component, OnInit, ViewChild, OnDestroy, TemplateRef } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { Subscription } from 'rxjs';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ThemePalette } from '@angular/material/core';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
const swal = require('sweetalert');
@Component({
  selector: 'app-model-deployment',
  templateUrl: './model-deployment.component.html',
  styleUrls: ['./model-deployment.component.scss'],
})
export class ModelDeploymentComponent implements OnInit, OnDestroy {
  dataSource: MatTableDataSource<[]>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild('myTable') table: MatTable<any>;

  pageSizeOptions: Array<number> = [3, 5, 10, 15, 20];
  displayedColumns: Array<string> = [];

  /** Subscription variables */
  hostSub$: Subscription;

  // Progress spinner settings
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;

  isEdit: Boolean = false;
  hostForm: FormGroup;
  dialogRef: MatDialogRef<any>;
  hostIdToUpdate: string;
  showSpinner: Boolean = false;

  constructor(
    private _host: ModelDeploymentService,
    private _toaster: ToastrService,
    private _fb: FormBuilder,
    private _dialog: MatDialog,
    private _spinner: SpinnerService
  ) {
    this.displayedColumns = ['srNo', 'hostName', 'hostUrl', 'createdAt', 'actions'];
    this.hostForm = this._fb.group({
      hostName: ['', Validators.required],
      url: [
        '',
        [
          Validators.required,
          Validators.pattern(
            '^((https?|ftp|smtp):\\/\\/)(www.)?[a-z0-9:.]+(.[a-z]+)*?[a-z0-9:]+(\\/[a-zA-Z0-9#]+\\/?)*$'
          ),
        ],
      ],
    });
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getHostList();
  }

  /**
   * Opens the host dialog
   * @param hostTemplate template reference
   * @param hostData
   */
  openHostModal(hostTemplate: TemplateRef<any>, hostData?: any) {
    if (hostData) {
      this.isEdit = true;
      this.hostIdToUpdate = hostData._id;
      /** Prefills the host form */
      this.hostForm.patchValue({
        hostName: hostData.hostName,
        url: hostData.url,
      });
    } else {
      this.hostForm.reset();
      this.isEdit = false;
    }
    this.dialogRef = this._dialog.open(hostTemplate, {
      minWidth: '50vw',
    });
  }

  /**
   * Handles the host operation as per the type - edit/new
   * @param formData
   */
  onSubmitHost(formData: any) {
    this.isEdit ? this.updateHostDetails(formData) : this.createNewHost(formData);
  }

  /**
   * Updates the host details
   * @param formData
   */
  updateHostDetails(formData: any): void {
    this.showSpinner = true;
    this.hostSub$ = this._host.updateHostDetails(formData, this.hostIdToUpdate).subscribe(
      (res) => {
        this.showSpinner = false;
        if (res) {
          this.getHostList();
          this._toaster.success('Host details updated successfully');
        } else {
          this._toaster.error('Cannot update host details', 'Model Deployment');
        }
      },
      (err) => {
        this.showSpinner = false;
        this._toaster.error('Server Error', 'Model Deployment');
      }
    );
  }

  /**
   * Creates a new host record into DB
   * @param formData host details
   */
  createNewHost(formData: any): void {
    this.showSpinner = true;
    this.hostSub$ = this._host.createNewHost(formData).subscribe(
      (res) => {
        this.showSpinner = false;
        if (res) {
          this._toaster.success('Host created successfully');
          this.getHostList();
        } else {
          this._toaster.error('Cannot create new host', 'Model Deployment');
        }
      },
      (err: HttpErrorResponse) => {
        this.showSpinner = false;
        this._toaster.error('Duplicate value(s) or server error', 'Model Deployment');
      }
    );
  }

  /**
   * Deletes the selected host
   * @param hostData
   */
  deleteHost(hostData: any): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this host!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        this.hostSub$ = this._host.deleteHost(hostData._id).subscribe(
          (res: any) => {
            if (res) {
              this.getHostList();
              this._toaster.success('Host delete successfully');
            } else {
              this._toaster.error('Cannot delete host', 'Model Deployment');
            }
          },
          (err: any) => {
            this._toaster.error('Server Error', 'Model Deployment');
          }
        );
      }
    });
  }
  /**
   * Fetches the host list from the DB
   */
  getHostList(): void {
    this.hostSub$ = this._host.getHostsList().subscribe(
      (res) => {
        if (res) {
          // Assign the data to the data source for the table to render
          this.dataSource = new MatTableDataSource(res);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          this._toaster.error('Cannot fetch hosts list', 'Model Deployment');
        }
        this._spinner.stopSpinner();
      },
      (err) => {
        this._spinner.stopSpinner();
        this._toaster.error('Server Error', 'Model Deployment');
      }
    );
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.hostSub$) {
      this.hostSub$.unsubscribe();
    }
  }
}
