import { MatSnackBar } from '@angular/material/snack-bar';
import { LocalStorageService } from '../../../../shared/local-storage.service';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ThemePalette } from '@angular/material/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { SampleTemplatesService } from '../sample-templates.service';
import { takeUntil } from 'rxjs/operators';
import { SampleTemplatesComponent } from '../sample-templates.component';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { MatChipInputEvent } from '@angular/material/chips';
import { DomainList } from 'src/app/shared/static-data';
@Component({
  selector: 'app-template-form',
  templateUrl: './template-form.component.html',
  styleUrls: ['./template-form.component.scss'],
})
export class TemplateFormComponent implements OnInit {
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  // Progress spinner settings
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  /** Default variables */
  isEdit: Boolean = false;
  showSpinner: Boolean = false;
  domainList: Array<any> = DomainList;

  templateForm: FormGroup;
  user: any;
  fileToUpload: any;
  allTemplateList: Array<any>;
  templateData: any;
  tagsArray: Array<any> = [];

  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    private _fb: FormBuilder,
    private _toaster: ToastrService,
    private _tempDialogRef: MatDialogRef<SampleTemplatesComponent>,
    private _localStorage: LocalStorageService,
    private _template: SampleTemplatesService,
    private _spinner: SpinnerService,
    private _snackbar: MatSnackBar
  ) {
    /** Template form instance creation */
    this.templateForm = this._fb.group({
      name: ['', [Validators.required, Validators.pattern('^[^\\s][A-Za-z0-9_\\- ]+$')]],
      domain: ['', Validators.required],
      description: ['', Validators.required],
    });
    this.allTemplateList = this.dialogData.allTemplateList;
    this.templateData = this.dialogData.templateData;
    this.prefillAndResetForm(this.templateData);
  }

  ngOnInit(): void {
    this.user = this._localStorage.getItem('user');
  }

  /**
   * Prefills or reset the template form as required
   * @param templateData selected template details
   */
  prefillAndResetForm(templateData: any): void {
    if (templateData) {
      this.isEdit = true;
      this.templateForm.patchValue({
        name: templateData.name,
        domain: templateData.domain,
        description: templateData.description,
      });
      this.tagsArray = templateData.tags;
      this.fileToUpload = this.templateData.file;
    } else {
      this.templateForm.reset();
      this.isEdit = false;
    }
  }

  /**
   * Handles the form submition
   * @param formValue template details
   */
  onSubmitTemplate(formValue: any): void {
    this._spinner.showSpinner();
    if (this.isEdit) {
      this.updateTemplateDetails(formValue);
    } else {
      this.createNewTemplate(formValue);
    }
  }

  /**
   * Creates a new user into the DB
   * @param formValue
   */
  createNewTemplate(formValue: any): void {
    const reqObj = {
      ...formValue,
      file: this.fileToUpload.name,
      createdBy: this.user._id,
      tags: this.tagsArray,
    };
    // API call
    this._template
      .createTemplate(this.fileToUpload, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._spinner.stopSpinner();
            this._tempDialogRef.close({ data: res.data, isEdit: this.isEdit });
            this._toaster.success('Sample project is created successfully', 'Sample Projects');
          } else {
            this._toaster.error('Cannot create a sample project', 'Sample Projects');
          }
        },
        (err: any) => {
          this._spinner.stopSpinner();
          this._tempDialogRef.close(null);
          this._toaster.error(err.error.error.message || 'Server Error', 'Sample Projects');
        }
      );
  }

  /**
   * Updates the template details
   * @param formValue
   */
  updateTemplateDetails(formValue: any): void {
    const reqObj = {
      ...formValue,
      file: this.fileToUpload.name,
      tags: this.tagsArray,
    };
    // API call
    this._template
      .updateTemplate(reqObj, this.templateData._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res.data) {
            this._spinner.stopSpinner();
            this._tempDialogRef.close({ data: res.data, isEdit: this.isEdit });
            this._toaster.success('Sample project is updated successfully', 'Sample Projects');
          } else {
            this._spinner.stopSpinner();
            this._tempDialogRef.close({ data: res.data, isEdit: this.isEdit });
            this._toaster.error('Cannot update sample project', 'Sample Projects');
          }
        },
        (err: any) => {
          this._spinner.stopSpinner();
          this._tempDialogRef.close(null);
          this._toaster.error('Server Error', 'Sample Projects');
        }
      );
  }

  /**
   * Stores the file data to be uploaded into the variable
   * @param inputFile files object
   */
  handleFileInput(inputFile: any) {
    const ext = inputFile[0].name.split('.').pop();
    if (inputFile && ['zip'].includes(ext)) {
      let isDuplicate: boolean = false;
      if (!this.isEdit) {
        this.allTemplateList.forEach((item: any) => {
          if (item.file === inputFile[0].name) {
            this._snackbar.open('File already exist', 'OK');
            isDuplicate = true;
          }
        });
        if (isDuplicate) {
          this.fileToUpload = null;
        } else {
          this.fileToUpload = inputFile[0];
        }
      } else {
        this.fileToUpload = inputFile[0];
      }
    } else {
      this.fileToUpload = null;
      this._snackbar.open(`.${ext} file type is not supported`, 'OK');
    }
  }

  /**
   * Adds the tag to the array
   * @param event
   */
  addTag(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;
    if (value.length >= input.minLength && value.length <= input.maxLength) {
      if ((value || '').trim()) {
        // Add our tags
        this.tagsArray.push(value.trim());
      }
      // Reset the input value
      if (input) {
        input.value = '';
      }
    }
  }

  /**
   * Removes the tag from array
   * @param tag
   */
  removeTag(tag: any): void {
    const index = this.tagsArray.indexOf(tag);
    if (index >= 0) {
      this.tagsArray.splice(index, 1);
    }
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
