import { ToastrService } from 'ngx-toastr';
import { SocketIoService } from 'src/app/core/socket-io.service';
import { UtilsService } from 'src/app/shared/utils.service';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { PlansService } from './plans/plans.service';
import { LocalStorageService } from './../../shared/local-storage.service';
import { DashboardService } from './dashboard.service';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import * as moment from 'moment';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { HttpErrorResponse } from '@angular/common/http';
import { TitleCasePipe } from '@angular/common';
import { MatDialog } from '@angular/material/dialog';
import { FeedbackFormComponent } from 'src/app/layout/feedback-form/feedback-form.component';
import { environment } from 'src/environments/environment';

const swal = require('sweetalert');
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],
})
export class DashboardComponent implements OnInit, OnDestroy {
  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  toggleButtons: boolean = false;
  isHomePage: boolean = true;
  showConnection: boolean = false;
  settingType: string;
  showBackButton: boolean = false;
  licenseDaysLeft: number;
  lincenseDetails: string;
  user: any;
  planDetails: any;
  daysLeft: any;
  saasDaysLeft: any;
  subsMsgDisplayed: string = null;
  alertData: string;
  subscription: any;
  hideHeading: boolean = false;
  paymentCaptured: boolean = false;
  projectLimit: any;
  projectCount: any;
  appType: string = environment.appType;
  feedbackProjectRewardCount: number = 0;
  feedbackTooltip: string = null;
  constructor(
    private _router: Router,
    private _dashboard: DashboardService,
    private _activatedRoute: ActivatedRoute,
    private _localStorage: LocalStorageService,
    private _plans: PlansService,
    private _spinner: SpinnerService,
    private _titleCasePipe: TitleCasePipe,
    private _utils: UtilsService,
    private _socket: SocketIoService,
    private _toaster: ToastrService,
    private _dialog: MatDialog
  ) {}

  ngOnInit(): void {
    this.user = this._localStorage.getItem('user');
    if (this.user) {
      this.feedbackProjectRewardCount = this.user?.psFeatures?.feedbackProjectRewardCount || 2;
      this.feedbackTooltip =
        'Submit feedback and earn ' + this.feedbackProjectRewardCount + ' extra projects!';
      this.paymentCaptured = this._localStorage.getItem('paymentCaptured');
      if (this.user.status === 'inactive' || this.user.status === 'created') {
        this._router.navigateByUrl('/dashboard/plans');
      } else {
        if (this.user.roles[0] === 'report_user') {
          this._router.navigate(['/dashboard/report']);
        } else if (this.user.roles[0] === 'pslite_user') {
          this._router.navigate(['/dashboard/pslite']);
        }
      }
      this.user.roles[0] === 'super_admin' ? this.getLicenseDetails() : this.getPlanDetails();
      this.getProjectCount();
      // Toggles the heading section as per the conditions
      if (
        this.user.roles[0] === 'report_user' ||
        this.user.roles[0] === 'pslite_user' ||
        this.user.status === 'inactive' ||
        this.user.status === 'created'
      ) {
        this.hideHeading = true;
      } else if (this.user.roles[0] === 'super_admin' && !this.user.subscription) {
        this.hideHeading = false;
      } else if (this.user.roles[0] !== 'super_admin' && this.user.subscription) {
        this.hideHeading = false;
      }
      this.initRequiredSetup();
    } else {
      this._router.navigateByUrl('/login');
    }
    // remove this call later when the plan thing is working fine
    // this.readPlanDetailsFromServer('basic');
  }

  /**
   * Assignes the settings name to variable as per the selected setting
   */
  initRequiredSetup(): void {
    if (this._activatedRoute.queryParams) {
      this._activatedRoute.queryParams.subscribe((val) => {
        if (Object.entries(val).length) {
          switch (val.type) {
            case 'db':
              this.settingType = 'Database Settings';
              break;
            case 'sftp':
              this.settingType = 'SFTP Settings';
              break;
            case 'modeldeploy':
              this.settingType = 'Model Deployment (Hosts)';
              break;
            case 'scheduler':
              this.settingType = 'Scheduler';
              break;
            case 'logs':
              this.settingType = 'Logs';
              break;
            case 'usecase':
              this.settingType = 'Usecase';
              break;
            case 'pscorenodes':
              this.settingType = 'PSCORE Nodes';
              break;
            case 'upload':
              this.settingType = 'Upload Model';
              break;
            case 'user':
              this.settingType = 'Users';
              break;
            case 'help':
              this.settingType = 'Help';
              break;
            case 'report':
              this.settingType = 'Report';
              break;
            case 'manageplans':
              this.settingType = 'Plans';
              break;
            case 'subscription':
              this.settingType = 'Subscription';
              break;
            case 'template':
              this.settingType = 'Sample Projects';
              break;
            case 'dataset':
              this.settingType = 'Sample Datasets';
              break;
            case 'profile':
              this.settingType = 'Profile';
              break;
            case 'algorithm':
              this.settingType = 'Algorithms';
              break;
            case 'snowflake':
              this.settingType = 'Snowflake Settings';
              break;
            case 's3':
              this.settingType = 'Amazon S3 Settings';
              break;
            case 'bigquery':
              this.settingType = 'BigQuery Settings';
              break;
          }
        } else {
          this.settingType = '';
        }
      });
    } else {
      this.settingType = '';
    }
  }

  /**
   * Opens the dialog with form
   */
  openFeedbackForm(): void {
    let saveDialog = this._dialog.open(FeedbackFormComponent, {
      maxWidth: '90vw',
      maxHeight: '90vh',
      data: {
        surveyType: 'rewardSurvey',
      },
    });
    saveDialog.afterClosed().subscribe((data) => {
      // if (data) {
      this.getProjectCount();
      //navigate to experiments list page after successful completion
      // }
    });
  }

  /**
   * Gets project limit and count for particular plan
   */
  getProjectCount(): void {
    this._dashboard
      .getProjectCount()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.projectLimit = res.projectLimit;
            this.projectCount = res.count;
          } else {
            this._toaster.info('Cannot fetch subscription details');
          }
        },
        (err: HttpErrorResponse) => {
          this._spinner.stopSpinner();
          this._toaster.info('Cannot fetch subscription details');
        }
      );
  }
  /**
   * Fetches the license details by triggering API
   */
  getLicenseDetails() {
    // console.log('hi this is the licesne details');
    this._dashboard.getLicenseInfo().subscribe(
      (res) => {
        const licExpData = moment(res.expDate).format('MMMM Do YYYY');
        const licActData = moment(res.actDate).format('MMMM Do YYYY');
        var limits = res.limits;
        this.lincenseDetails = `Expiry Date : ${licExpData} \n 
                                Activation Date : ${licActData} \n 
                                ${
                                  environment.appType === 'enterprise'
                                    ? `Project Limit : ${limits.projectLimit} Projects \n\n Training Limit : ${limits.trainingLimit} Trainings`
                                    : ''
                                }`;
        this.licenseDaysLeft = Math.abs(moment().diff(res.expDate, 'days'));
        this._spinner.stopSpinner();
      },
      (err) => {
        this._spinner.stopSpinner();
        this.lincenseDetails = 'Server error';
      }
    );
  }

  /**
   * Fetches the plan details of logged-in user
   */
  getPlanDetails(): void {
    // console.log('hi this is the plan details');
    if (this.user.subscription) {
      this._plans
        .getSubscriptionDetails(this.user._id, this.user.subscription)
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(
          (res: any) => {
            if (res) {
              this.planDetails = res;
              this.createPlanDetails(res);
              this._utils.userSubscriptionSubject(res);
            } else {
              this._toaster.info('Cannot fetch subscription details');
            }
          },
          (err: HttpErrorResponse) => {
            this._spinner.stopSpinner();
            this.alertData = 'Cannot fetch subscription details';
          }
        );
    } else {
      this.saasDaysLeft = Math.abs(moment().diff(this.user.validity, 'days'));
    }
  }

  /**
   * Generates the plan details required format
   * @param data
   */
  createPlanDetails(data: any): void {
    const expData = moment(data.planEnd);
    const actData = moment(data.planStart);
    this.daysLeft = expData.diff(actData, 'days');
    this.saasDaysLeft = expData.diff(moment(), 'days');
    // console.log('🚀 ~ this.daysLeft ---->', this.daysLeft);
    this.alertData = `Type : ${
      data.isFreeTrial ? 'Pro' : this._titleCasePipe.transform(data.planType)
    } \n
    Status : ${this._titleCasePipe.transform(data.status)} \n
    Activation Date : ${actData.format('MMMM Do YYYY')} \n
    Expiry Date : ${expData.format('MMMM Do YYYY')}`;
    // Duration : ${data.isFreeTrial ? '-' : this._titleCasePipe.transform(data.planDuration)} \n
  }

  /**
   * Generates the proper message as per conditions
   * @returns
   */
  getProperMessage(): string {
    let isSame: boolean = false;
    // Check whether present date is same as plan end date
    if (new Date(this.planDetails?.planEnd) > new Date()) {
    } else {
      isSame = true;
    }
    if (this.planDetails?.isFreeTrial && this.planDetails?.status === 'active') {
      this.subsMsgDisplayed = isSame
        ? 'Your free trial will be ending today.'
        : `Your free trial will end in ${this.saasDaysLeft} days.`;
    } else if (!this.planDetails?.isFreeTrial && this.planDetails?.status === 'active') {
      this.subsMsgDisplayed = isSame
        ? 'Your plan will expiring today.'
        : `Your plan will expire in ${this.saasDaysLeft} days.`;
    }
    return this.subsMsgDisplayed;
  }

  /**
   * Modal to render the license information
   */
  showLicenseInfo() {
    swal({
      title: 'License Information',
      text: this.lincenseDetails,
      icon: 'info',
      button: false,
    });
  }

  /**
   * Model to render the plan info
   */
  showPlanInfo() {
    swal({
      title: 'Plan Information',
      text: this.alertData,
      icon: 'info',
      button: false,
    });
  }

  /**
   * Navigates to the selected UI
   * @param type
   */
  navigate(type: string): void {
    switch (type) {
      case 'users':
        this._router.navigate(['/dashboard/users']);
        this.settingType = 'Users';
        break;
      case 'help':
        this._router.navigate(['/dashboard/help']);
        this.settingType = 'Help';
        break;
      case 'report':
        this._router.navigate(['/dashboard/reports']);
        this.settingType = 'Reports';
        break;
      default:
        this._router.navigate(['/dashboard']);
        this.settingType = '';
    }
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
