import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ThemePalette } from '@angular/material/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { UsecaseListService } from '../usecase-list.service';

@Component({
  selector: 'app-domain',
  templateUrl: './domain.component.html',
  styleUrls: ['./domain.component.scss'],
})
export class DomainComponent implements OnInit {
  dataSource: MatTableDataSource<[]>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild('myTable') table: MatTable<any>;
  pageSizeOptions: Array<number> = [3, 5, 10, 15, 20];
  displayedColumns: Array<string> = [];

  /** Subscription letiables */
  domainSub$: Subscription;

  /** Progress spinner settings */
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;

  isEdit: Boolean = false;
  domainForm: FormGroup;
  dialogRef: MatDialogRef<any>;
  showSpinner: boolean = false;
  domainIdToUpdate: string;
  dataToUpdate: any;

  constructor(
    private _usecaseList: UsecaseListService,
    private _toaster: ToastrService,
    private _fb: FormBuilder,
    private _dialog: MatDialog
  ) {
    this.displayedColumns = ['srNo', 'domainName', 'createdAt', 'actions'];
    this.getDomainList();
    this.domainForm = this._fb.group({
      domainName: ['', Validators.required],
    });
  }

  ngOnInit(): void { }

  /**
   * Opens the domain modal
   * @param DomainTemplate
   */
  openDomainModal(DomainTemplate: TemplateRef<any>, domainData?: any) {
    if (domainData) {
      this.isEdit = true;
      this.domainIdToUpdate = domainData._id;
      this.dataToUpdate = domainData;
      this.domainForm.patchValue({
        domainName: domainData.name,
      });
    } else {
      this.domainForm.reset();
      this.isEdit = false;
    }
    this.dialogRef = this._dialog.open(DomainTemplate, {
      minWidth: '50vw',
    });
  }

  /**
   * Creates a new domain
   * @param formData
   */
  onSubmitDomain(formData: any): void {
    this.isEdit ? this.updateDomain(formData) : this.createNewDomain(formData);
  }

  /**
   * Creates a new domain
   * @param formData
   */
  createNewDomain(formData: any) {
    this.showSpinner = true;
    formData.name = formData.domainName;
    formData.imgPath = 'img/' + formData.domainName + '.png';
    this.domainSub$ = this._usecaseList.createNewDomain(formData).subscribe(
      (res: any) => {
        this.showSpinner = false;
        if (res) {
          this._toaster.success('Domain created successfully', 'Domain Setting');
          this.getDomainList();
        } else {
          this._toaster.error('Cannot create new domain', 'Domain Setting');
        }
      },
      (err: any) => {
        this.showSpinner = false;
        this._toaster.error('Duplicate Domain Name', 'Domain Setting');
      }
    );
  }

  /**
   * Fetches the domain list
   */
  getDomainList(): void {
    this.domainSub$ = this._usecaseList.getDomainList().subscribe(
      (res: any) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          this._toaster.error('Cannot fetch the domain list', 'Domain Setting');
        }
      },
      (err: any) => {
        this._toaster.error('Server Error', 'Domain Setting');
      }
    );
  }
  /**
   * Updates the domain details
   * @param formData
   */
  updateDomain(formData: any) {
    this.showSpinner = true;
    this.dataToUpdate.name = formData.domainName;
    this.dataToUpdate.imgPath = `img/${formData.domainName}.png`;
    let reqObj: any = {
      domain: this.dataToUpdate,
      domainId: this.domainIdToUpdate,
    };
    // API call
    this.domainSub$ = this._usecaseList.updatedDomain(reqObj, this.domainIdToUpdate).subscribe(
      (res: any) => {
        this.showSpinner = false;
        if (res) {
          this._toaster.success('Domain updated successfully', 'Domain Setting');
          this.getDomainList();
        } else {
          this._toaster.error('Cannot update domain', 'Domain Setting');
        }
      },
      (err: any) => {
        this.showSpinner = false;
        this._toaster.error('Server Error', 'Domain Setting');
      }
    );
  }

  /**
   * Deletes the domain
   * @param domainData
   */
  deleteDomain(domainData: any): void {
    this.domainSub$ = this._usecaseList.deleteDomain(domainData._id).subscribe(
      (res: any) => {
        if (res) {
          this.getDomainList();
          this._toaster.success('Domain deleted successfully');
        } else {
          this._toaster.error('Cannot delete domain', 'Domain Setting');
        }
      },
      (err: any) => {
        this._toaster.error('Server Error', 'Domain Setting');
      }
    );
  }
  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.domainSub$) {
      this.domainSub$.unsubscribe();
    }
  }
}
