import { SpinnerService } from 'src/app/shared/spinner.service';
import { HttpErrorResponse } from '@angular/common/http';
import { CustomValidators } from 'ngx-custom-validators';
import { Component, ElementRef, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ThemePalette } from '@angular/material/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { UploadModelService } from './upload-model.service';
import { environment } from 'src/environments/environment';
import { MatSnackBar } from '@angular/material/snack-bar';
const swal = require('sweetalert');
@Component({
  selector: 'app-upload-model',
  templateUrl: './upload-model.component.html',
  styleUrls: ['./upload-model.component.scss'],
})
export class UploadModelComponent implements OnInit {
  dataSource: MatTableDataSource<[]>;
  @ViewChild('fileInput') inputVariable: ElementRef;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild('myTable') table: MatTable<any>;
  pageSizeOptions: Array<number> = [3, 5, 10, 15, 20];
  displayedColumns: Array<string> = [];
  InputVar: ElementRef;
  /** Subscription letiables */
  uploadmodelSub$: Subscription;

  /** Progress spinner settings */
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;
  dialogRef: any;
  fileToUpload: any;
  uploadType: string = 'urlUpload';
  any;
  isEdit: Boolean = false;
  showSpinner: boolean = false;
  uploadModelForm: FormGroup;
  spacy = 'spacy';
  taskTypeList: any = [
    {
      taskName: 'NER',
      taskValue: 'ner',
    },
    {
      taskName: 'POS Tagging',
      taskValue: 'pos',
    },
    {
      taskName: 'Rule Based Match',
      taskValue: 'ruleBased',
    },
  ];
  constructor(
    private _taModel: UploadModelService,
    private _toaster: ToastrService,
    private _fb: FormBuilder,
    private _dialog: MatDialog,
    private _window: Window,
    private _spinner: SpinnerService,
    private _snackbar: MatSnackBar,
  ) {
    this.displayedColumns = ['srNo', 'modelName', 'frameWork', 'createdAt', 'actions'];
    this.uploadModelForm = this._fb.group({
      modelName: ['', Validators.required],
      uploadUrl: ['', [Validators.required, CustomValidators.url]],
      selectedFrameWork: ['', Validators.required],
      selectedTaskType: ['', Validators.required],
    });
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getTaModelList();
  }

  /**
   * Fetches the usecase list
   */
  getTaModelList(): void {
    this.uploadmodelSub$ = this._taModel.getTaModelList().subscribe(
      (res: any) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          this._toaster.error('Cannot fetch the usecase list', 'Upload Model Setting');
        }
        this._spinner.stopSpinner();
      },
      (err: any) => {
        this._spinner.stopSpinner();
        this._toaster.error('Server Error', 'Usecase Setting');
      }
    );
  }

  /**
   * Stores the file data to be uploaded into the variable
   * @param inputFile files object
   */
  handleFileInput(inputFile: any) {
    const ext = inputFile[0].name.split('.').pop();
    if (!['zip'].includes(ext)) {
      this.inputVariable.nativeElement.value = "";
      this.fileToUpload = '';
      this._snackbar.open(`Only zip file type is supported`, 'OK');
      return;
    }
    if (inputFile) {
      this.fileToUpload = inputFile[0];
    }
  }

  /**
   * Called when the tab is changed in the UI
   * @param event
   */
  tabChanged(event: any): void {
    if (event.tab.textLabel === 'From My Computer') {
      this.uploadType = 'fileUpload';
      this.uploadModelForm.reset();
      this.uploadModelForm.get('modelName').clearValidators();
      this.uploadModelForm.get('modelName').updateValueAndValidity();
      this.uploadModelForm.get('uploadUrl').clearValidators();
      this.uploadModelForm.get('uploadUrl').updateValueAndValidity();
      this.uploadModelForm.get('selectedFrameWork').setValidators(Validators.required);
      this.uploadModelForm.get('selectedFrameWork').updateValueAndValidity();
      this.uploadModelForm.get('selectedTaskType').setValidators(Validators.required);
      this.uploadModelForm.get('selectedTaskType').updateValueAndValidity();
    } else {
      this.uploadType = 'urlUpload';
      this.uploadModelForm.reset();
      this.uploadModelForm.get('modelName').setValidators(Validators.required);
      this.uploadModelForm.get('modelName').updateValueAndValidity();
      this.uploadModelForm.get('uploadUrl').setValidators(Validators.required);
      this.uploadModelForm.get('uploadUrl').updateValueAndValidity();
    }
  }

  onSubmit(formData: any) {
    this.showSpinner = true;
    let reqObj;
    if (!formData.uploadUrl) {
      reqObj = {
        selectedFrameWork: formData.selectedFrameWork,
        selectedTaskType: formData.selectedTaskType,
        file: this.fileToUpload,
      };
    } else {
      if (formData.selectedTaskType) {
        reqObj = {
          uploadUrl: formData.uploadUrl,
          modelName: formData.modelName,
          frameWork: formData.selectedFrameWork,
          taskType: formData.selectedTaskType,
        };
      } else {
        reqObj = {
          uploadUrl: reqObj.uploadUrl,
          modelName: reqObj.modelName,
          frameWork: reqObj.selectedFrameWork,
        };
      }
    }
    this.uploadmodelSub$ = this._taModel.uploadTaModel(reqObj).subscribe(
      (res: any) => {
        if (res) {
          this.getTaModelList();
          this.showSpinner = false;
          this._toaster.success('Model Uploaded Successfully', 'Upload Model Setting');
        } else {
          this.showSpinner = false;
          this._toaster.error('Cannot upload model', 'Upload Model Setting');
        }
        this.uploadModelForm.reset();
      },
      (err: HttpErrorResponse) => {
        this.showSpinner = false;
        this.uploadModelForm.reset();
        this._toaster.error(JSON.parse(err.error).message || 'Server Error', 'Upload model');
      }
    );
  }

  deleteModel(row: any) {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this model!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        this.uploadmodelSub$ = this._taModel.deleteTaModel(row._id).subscribe(
          (res: any) => {
            if (res) {
              this.getTaModelList();
              this.showSpinner = false;
              this._toaster.success('Model deleted successfully', 'Upload model');
            } else {
              this.showSpinner = false;
              this._toaster.error('Cannot delete model', 'Upload model');
            }
          },
          (err: any) => {
            this.showSpinner = false;
            this._toaster.error('Server Error', 'Upload model');
          });
      }
    });
}

  downloadModel(row: any) {
    let modelId = row._id;
    this._window.open(`${environment.serverBaseUrl}/api/v2/model/${modelId}/download`, '_blank');
  }

  /**
   * Opens the usecase modal
   * @param uploadModelTemplate
   */
  openUploadModal(uploadModelTemplate: TemplateRef<any>) {
    this.dialogRef = this._dialog.open(uploadModelTemplate, {
      minWidth: '50vw',
    });
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.uploadmodelSub$) {
      this.uploadmodelSub$.unsubscribe();
    }
  }
}
