import { LocalStorageService } from './../../../../shared/local-storage.service';
import { RegisterService } from '../../../pages/register/register.service';
import { UsersService } from '../users.service';
import { Component, Inject, OnInit, TemplateRef } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ThemePalette } from '@angular/material/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { ToastrService } from 'ngx-toastr';
import { UsersComponent } from '../users.component';
import { Subscription } from 'rxjs';
import { environment } from 'src/environments/environment';
import { PsCustomValidators } from 'src/app/shared/ps-custom-validators';
import { CustomValidators } from 'ngx-custom-validators';
import * as moment from 'moment';

@Component({
  selector: 'app-users-form',
  templateUrl: './users-form.component.html',
  styleUrls: ['./users-form.component.scss'],
})
export class UsersFormComponent implements OnInit {
  // Progress spinner settings
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;

  hidePassword: Boolean = true;
  hideConfirmPassword: Boolean = true;
  usersSub$: Subscription;
  registerSub$: Subscription;
  userForm: FormGroup;
  resetPasswordForm: FormGroup;
  showSpinner: Boolean = false;
  isEdit: Boolean = false;
  connectionIdToUpdate: string;
  userIdToUpdate: string;
  isApiPermission: Boolean = false;
  isFormValid: Boolean = false;
  resetDialogRef: MatDialogRef<any>;
  userTypesList: Array<object> = [
    { name: 'Admin', value: 'admin' },
    { name: 'Developer', value: 'developer' },
    { name: 'Report User', value: 'report_user' },
    // { name: 'PsLite User', value: 'pslite_user' },
  ];
  env = environment;
  user: any;
  passwordControl: FormControl;
  todayDate: string;

  /** Reset password form control instance */
  resetPasswordControl = new FormControl('', [
    Validators.required,
    Validators.maxLength(32),
    Validators.minLength(8),
    Validators.pattern('^[^\\s]+'),
    PsCustomValidators.passwordPolicies,
  ]);

  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    _fb: FormBuilder,
    private _users: UsersService,
    private _toaster: ToastrService,
    private _userDialogRef: MatDialogRef<UsersComponent>,
    private _register: RegisterService,
    private _dialog: MatDialog,
    private _localStorage: LocalStorageService
  ) {
    /** Password form control instance */
    this.passwordControl = new FormControl(
      '',
      this.dialogData.userData
        ? null
        : [
            Validators.required,
            Validators.maxLength(32),
            Validators.minLength(8),
            Validators.pattern('^[^\\s]+'),
            PsCustomValidators.passwordPolicies,
          ]
    );
    /** User form instance creation */
    this.userForm = _fb.group({
      // roles: ['', Validators.required],
      firstName: ['', [Validators.required, Validators.pattern('^[^\\s][A-Za-z0-9 ]*$')]],
      lastName: ['', [Validators.required, , Validators.pattern('^[^\\s][A-Za-z0-9 ]*$')]],
      email: ['', [Validators.required, Validators.email]],
      password: this.passwordControl,
      confirmPassword: [
        '',
        dialogData.userData
          ? ''
          : [Validators.required, CustomValidators.equalTo(this.passwordControl)],
      ],
      username: ['', dialogData.userData ? '' : [Validators.required, Validators.pattern('^[^\\s][A-Za-z0-9 ]*$')]],
      apiName1: [{ value: '', disabled: true }],
      apiName2: [{ value: '', disabled: true }],
      count1: [''],
      count2: [''],
      validity: [''],
      isEnabled: [''],
    });
    // this.userForm.addControl('roles', Validators.required)
    if (this.env.appType !== 'saas') {
      this.userForm.addControl('roles', new FormControl('', Validators.required));
    }
    /** Reset password form instance creation */
    this.resetPasswordForm = _fb.group({
      userName: [
        this.dialogData.userData ? this.dialogData.userData.username : '',
        Validators.required,
      ],
      newPassword: this.resetPasswordControl,
      verifyPassword: [
        '',
        [Validators.required, CustomValidators.equalTo(this.resetPasswordControl)],
      ],
    });
    this.prefillUserForm(this.dialogData.userData);
  }

  ngOnInit(): void {
    this.todayDate = String(moment().format('YYYY-MM-DD'));
    this.user = this._localStorage.getItem('user');
  }

  /**
   * Prefill the users form for edit functionality
   * @param userData selected users details
   */
  prefillUserForm(userData: any): void {
    if (userData) {
      this.isEdit = true;
      this.userIdToUpdate = userData._id;
      this.userForm.patchValue({
        roles: userData.roles,
        firstName: userData.firstName,
        lastName: userData.lastName,
        email: userData.email,
        validity: userData.validity ? moment().add(userData.validity, 'days').format('YYYY-MM-DD') : null,
        isEnabled: userData.isEnabled,
      });
      this.userForm.updateValueAndValidity();
    } else {
      this.userForm.reset();
      this.isEdit = false;
    }
  }

  /**
   * Api permission fields validation and defaut values
   * @param isChecked
   */
  apiPermissionChanged(isChecked: Boolean): void {
    if (isChecked) {
      this.userForm.get('apiName1').setValue('get output');
      this.userForm.get('apiName1').disable();
      this.userForm.get('apiName2').setValue('get prediction');
      this.userForm.get('apiName2').disable();
      this.userForm.get('count1').setValidators(Validators.required);
      this.userForm.get('count2').setValidators(Validators.required);
    } else {
      this.userForm.get('count1').clearValidators();
      this.userForm.get('count1').updateValueAndValidity();
      this.userForm.get('count2').clearValidators();
      this.userForm.get('count2').updateValueAndValidity();
    }
  }

  /**
   * Handles the form submition
   * @param formValue user details
   */
  onSubmitUser(formValue: any): void {
    if (this.isEdit) {
      this.updateUserDetails(formValue);
    } else {
      this.createNewUser(formValue);
    }
  }

  /**
   * Generates the API request data structure for users
   * @param userDetails
   */
  generateRequestData(userDetails: any): object {
    let apiOptions = [
      { apiName: 'get output', count: userDetails.count1, consumed: 0 },
      { apiName: 'get prediction', count: userDetails.count2, consumed: 0 },
    ];

    delete userDetails.apiName1;
    delete userDetails.apiName2;
    delete userDetails.count1;
    delete userDetails.count2;
    this.isEdit ? delete userDetails.password : null;
    this.isEdit ? delete userDetails.confirmPassword : null;
    this.isEdit ? delete userDetails.username : null;

    let finalUserData: any = {
      api: this.isApiPermission,
      ...userDetails,
    };
    this.isApiPermission ? (finalUserData.data = apiOptions) : null;
    return finalUserData;
  }

  /**
   * Creates a new user into the DB
   * @param formValue
   */
  createNewUser(formValue: any): void {
    let structuredData = this.generateRequestData(formValue);
    this.registerSub$ = this._register.registerUser(structuredData).subscribe(
      (res: any) => {
        if (res) {
          this._userDialogRef.close(JSON.stringify(res));
          environment.appType === 'saas' && this.user.roles[0] !== 'super_admin'
            ? this._toaster.success(
                `An invitation link has been successfully sent to ${res.displayName} on ${res.email}`,
                'Invite User'
              )
            : this._toaster.success('User is created successfully', 'Users Setting');
        } else {
          this._toaster.error('Cannot create a new user', 'Users Setting');
        }
      },
      (err: any) => {
        this._toaster.error(`${err.error.message}`, 'Users Setting');
      }
    );
  }

  /**
   * Updates the user details
   * @param formValue
   */
  updateUserDetails(formValue: any): void {
    let structuredData = this.generateRequestData(formValue);
    this.usersSub$ = this._users.updateUser(structuredData, this.userIdToUpdate).subscribe(
      (res: any) => {
        if (res) {
          this._userDialogRef.close(JSON.stringify(res));
          this._toaster.success('User updated successfully', 'Users Setting');
        } else {
          this._toaster.error('Cannot update user details', 'Users Setting');
        }
      },
      (err: any) => {
        this._toaster.error('Server Error', 'Users Setting');
      }
    );
  }

  /**
   * Opens the reset password modal
   * @param resetPasswordModal template reference
   */
  openResetPasswordModal(resetPasswordModal: TemplateRef<any>) {
    this._userDialogRef.close();
    this.resetDialogRef = this._dialog.open(resetPasswordModal, {
      minWidth: '50%',
    });
  }

  /**
   * Close the reset password modal
   */
  closeResetPasswordModal(): void {
    this.resetDialogRef.close();
  }

  /**
   * Resets the users password by calling API
   */
  resetUserPassword(formValue: any): void {
    this.showSpinner = true;
    let finalData = {
      uid: this.dialogData.userData._id,
      aid: '60239cd4d6f61e467256b82d',
      ...formValue,
    };
    this.usersSub$ = this._users.resetPassword(finalData).subscribe(
      (res: any) => {
        if (res) {
          this.showSpinner = false;
          this.resetDialogRef.close();
          this._toaster.success('Password reset successfully', 'Users Setting');
        } else {
          this.showSpinner = false;
          this.resetDialogRef.close();
          this._toaster.error('Cannot reset password', 'Users Setting');
        }
      },
      (err: any) => {
        this.showSpinner = false;
        this.resetDialogRef.close();
        this._toaster.error(`${err.error.message}`, 'Users Setting');
      }
    );
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.usersSub$) {
      this.usersSub$.unsubscribe();
    } else if (this.registerSub$) {
      this.registerSub$.unsubscribe();
    }
  }
}
