import { SpinnerService } from 'src/app/shared/spinner.service';
import { LogsService } from './logs.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ThemePalette } from '@angular/material/core';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { ToastrService } from 'ngx-toastr';
import { PscoreNodesService } from '../pscore-nodes/pscore-nodes.service';

@Component({
  selector: 'app-logs',
  templateUrl: './logs.component.html',
  styleUrls: ['./logs.component.scss'],
})
export class LogsComponent implements OnInit {
  /* Spinner settings */
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 70;

  psstudioForm: FormGroup;
  psstudioRangeGroup: FormGroup;
  pscoreRangeGroup: FormGroup;
  pscoreForm: FormGroup;
  osForm: FormGroup;
  isPrettifyPsstudio: Boolean = true;
  isPrettifyPscore: Boolean = true;
  nodesList: Array<any>;
  showPsstudioSpinner: Boolean = false;
  showPscoreSpinner: Boolean = false;
  showOSSpinner: Boolean = false;

  constructor(
    private _fb: FormBuilder,
    private _logs: LogsService,
    private _toaster: ToastrService,
    private _window: Window,
    private _node: PscoreNodesService,
    private _spinner: SpinnerService
  ) {
    this.psstudioRangeGroup = new FormGroup({
      start: new FormControl('', Validators.required),
      end: new FormControl('', Validators.required),
    });

    this.pscoreRangeGroup = new FormGroup({
      start: new FormControl('', Validators.required),
      end: new FormControl('', Validators.required),
    });

    this.psstudioForm = this._fb.group({
      limit: ['', Validators.required],
      order: ['', Validators.required],
    });

    this.pscoreForm = this._fb.group({
      node: ['', Validators.required],
    });

    this.osForm = this._fb.group({
      order: ['', Validators.required],
    });
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getNodesList();
  }

  /**
   * Fetches the pscore nodes list
   */
  getNodesList() {
    this._node.getPscoreNodeList().subscribe(
      (res: any) => {
        if (res) {
          this.nodesList = res;
        } else {
          this._toaster.error('Cannot fetch nodes list', 'Log Options');
        }
        this._spinner.stopSpinner();
      },
      (err: Error) => {
        this._spinner.stopSpinner();
        this._toaster.error('Server Error', 'Log Options');
      }
    );
  }

  /**
   * Exports the PSSTUDIO logs
   * @param formData
   */
  exportPsstudioLogs(formData: any): void {
    this.showPsstudioSpinner = true;
    formData.startDate = this.pscoreRangeGroup.get('start').value;
    formData.endDate = this.pscoreRangeGroup.get('end').value;
    this._logs.exportPsstudioLogs({ exportOptions: { ...formData } }).subscribe(
      (res: any) => {
        if (res) {
          this.showPsstudioSpinner = false;
          this.openNewBrowser(res, 'PS');
        } else {
          this.showPsstudioSpinner = false;
          this._toaster.error('Cannot export PS Studio logs', 'Log Options');
        }
      },
      (err: Error) => {
        this.showPsstudioSpinner = false;
        this._toaster.error('Server Error', 'Log Options');
      }
    );
  }

  /**
   * Downloads the pscore nodes logs
   * @param formData
   */
  exportPscoreLogs(formData: any): void {
    this.showPscoreSpinner = true;
    this._logs.exportPscoreLogs(formData.node).subscribe(
      (res: any) => {
        if (res) {
          this.showPscoreSpinner = false;
          this.openNewBrowser(res, 'core');
        } else {
          this.showPscoreSpinner = false;
          this._toaster.error('Cannot export PS Studio logs', 'Log Options');
        }
      },
      (err: Error) => {
        this.showPscoreSpinner = false;
        this._toaster.error('Server Error', 'Log Options');
      }
    );
  }

  /**
   * Exports the operating system logs
   * @param formData
   */
  exportOSLogs(formData: any): void {
    this.showOSSpinner = true;
    formData.startDate = this.pscoreRangeGroup.get('start').value;
    formData.endDate = this.pscoreRangeGroup.get('end').value;
    this._logs.exportOSLogs({ exportOptions: { ...formData } }).subscribe(
      (res: any) => {
        if (res) {
          this.showOSSpinner = false;
          this.openNewBrowser(res, 'OS');
        } else {
          this.showOSSpinner = false;
          this._toaster.error('Cannot export PS Studio logs', 'Log Options');
        }
      },
      (err: Error) => {
        this.showOSSpinner = false;
        this._toaster.error('Server Error', 'Log Options');
      }
    );
  }

  /**
   * Opens the new browser to downlaod the log file
   */
  openNewBrowser(res: any, logType: string): void {
    let fileName: string;
    if (logType === 'PS') {
      fileName = 'PredictStudio.log';
    } else if (logType === 'core') {
      fileName = 'Pscore.log';
    } else {
      fileName = 'Operating System.log';
      res = res.logs;
    }

    let logsData =
      this.isPrettifyPscore || this.isPrettifyPsstudio
        ? JSON.stringify(res, null, 4)
        : JSON.stringify(res);

    var blob = new Blob([logsData], { type: 'text/json' });
    if (this._window.navigator && this._window.navigator.msSaveOrOpenBlob) {
      this._window.navigator.msSaveOrOpenBlob(blob);
    } else {
      let e = document.createEvent('MouseEvents');
      let a = document.createElement('a');
      a.download = fileName;
      a.href = URL.createObjectURL(blob);
      a.dataset.downloadurl = ['text/json', a.download, a.href].join(':');
      e.initEvent('click', true, false);
      a.dispatchEvent(e);
    }
  }
}
