import { LocalStorageService } from './../../shared/local-storage.service';
import { Injectable } from '@angular/core';
import {
  CanActivate,
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  UrlTree,
  Router,
} from '@angular/router';
import { Observable } from 'rxjs';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root',
})
export class PlanExpiredAuthGuard implements CanActivate {
  constructor(
    private _authService: AuthService,
    private _router: Router,
    private _localStorage: LocalStorageService
  ) {}

  /**
   * Restricts the user to navigate to the login page again, if already logged in
   * @param route
   * @param state
   * @returns
   */
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    let user: any = this._localStorage.getItem('user');
    if (
      user &&
      user.roles[0] !== 'super_admin' &&
      user.roles[0] !== 'report_user' &&
      user.roles[0] !== 'pslite_user' &&
      user.roles[0] !== 'admin'
      // user.roles[0] !== 's_developer' &&
      // user.roles[0] === 's_admin'
    ) {
      return true;
    } else {
      this._router.navigateByUrl('/dashboard');
      return false;
    }
  }
}
