import { SampleTemplatesService } from './../sample-templates/sample-templates.service';
import { environment } from 'src/environments/environment';
import { SpinnerService } from '../../../shared/spinner.service';
import { Component, OnInit, TemplateRef, ViewChild, OnDestroy } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { Router } from '@angular/router';
import { ThemePalette } from '@angular/material/core';
import { ToastrService } from 'ngx-toastr';
import { DashboardService } from '../dashboard.service';
import { Subject, Subscription } from 'rxjs';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { UtilsService } from 'src/app/shared/utils.service';
import { takeUntil } from 'rxjs/operators';
import { CustomValidators } from 'ngx-custom-validators';
import { UsersService } from '../users/users.service';
import { DashboardComponent } from '../dashboard.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DomainList } from 'src/app/shared/static-data';

const swal = require('sweetalert');
@Component({
  selector: 'app-project-management',
  templateUrl: './project-management.component.html',
  styleUrls: ['./project-management.component.scss'],
})
export class ProjectManagementComponent implements OnInit, OnDestroy {
  @ViewChild(MatPaginator) paginator: MatPaginator;

  // Usubscription subject
  unsubscribe$ = new Subject<boolean>();

  // Settings for pagination
  pageEvent: PageEvent;
  pageSizeOptions: Array<number> = [3, 5, 10, 15, 20];
  pageSize: number = 5;
  pageIndex: number = 0;

  // Progress spinner settings
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;
  exportProjectDiameter = 50;

  // HTTP API's subscription variables
  dashboardSub$: Subscription;

  // Form Groups
  projectForm: FormGroup;
  projectTypeControl = new FormControl('predictive_modeling');
  emailFormControl = new FormControl('', CustomValidators.email);

  allProjectsList: Array<object> = [];
  filteredProjectsList: Array<object> = [];
  useGPU: boolean = false;
  isGpuAvailable: boolean = false;
  fileToUpload: FileList;
  dialogRef: MatDialogRef<ProjectManagementComponent>;
  isEdit: boolean = false;
  projectIdToUpdate: string;
  showExportProjectSpinner: boolean = false;
  downloadLink: string;
  showExportBtn: boolean = false;
  downloadFileName: string;
  exportingProjectDetails: any;
  commentsProjectDetails: any;
  commentsArray: Array<object> = [];
  commentMessage: string = '';
  sharingProjectDetails: any;
  user: any;
  searchString: string;
  // saas restriction variables
  allowedPSFeaturesList: Array<string> = null;
  allowedProjectTypes: Array<any> = null;
  allowedProjectmanagementActions: Array<any> = null;
  appType: string = environment.appType;
  allSampleProjects: Array<any>;
  filterSampleProjects: Array<any>;
  userList: Array<any> = null;
  showProjectDetails: boolean = false;
  detailsToBeShown: any;
  domainList: Array<any>;
  domainFilter: string = 'all';

  constructor(
    private _dashboard: DashboardService,
    private _dialog: MatDialog,
    private _fb: FormBuilder,
    private _router: Router,
    private _toaster: ToastrService,
    private _window: Window,
    private _spinner: SpinnerService,
    private _localStorage: LocalStorageService,
    public _utils: UtilsService,
    private _sampleTemplate: SampleTemplatesService,
    private _users: UsersService,
    public _dashboardComp: DashboardComponent,
    private _snackbar: MatSnackBar
  ) {
    this.user = this._localStorage.getItem('user');
    /**
     * Form builder to generate a dynamic reactive form
     */
    this.projectForm = this._fb.group({
      name: [
        '',
        [
          Validators.required,
          Validators.pattern('^[^\\s][A-Za-z0-9_\\- ]+$'),
          Validators.maxLength(60),
        ],
      ],
      projectDescription: [CustomValidators.url],
      type: this.projectTypeControl,
    });
  }

  /**
   * Initializes the page with all requirements
   */
  ngOnInit(): void {
    this.domainList = [{ name: 'All', value: 'all' }, ...new Set(DomainList)];
    // calling the set restriction function
    environment.appType === 'saas' &&
      !this.user?.roles?.includes('super_admin') &&
      !this.user?.roles?.includes('admin') &&
      this.setRestrictions();
    this.getAllProjects();
    this.getAllSampleProjects();
    this.findMachineDetails();
    // fetching the user list for displaying in share project dorpdown.
    this.getUsersList();
  }

  /**
   * Fetches the users list from DB
   */
  getUsersList() {
    this._users.getUsersList().subscribe(
      (res) => {
        if (res) {
          this._spinner.stopSpinner();
          // Assign the data to the data source for the table to render
          this.userList = res;
        } else {
          this._spinner.stopSpinner();
          this._toaster.error('Cannot fetch users list', 'Project Management');
        }
      },
      (err) => {
        this._spinner.stopSpinner();
        this._toaster.error('Server Error', 'Project Management');
      }
    );
  }

  /**
   * function to add restrictions to the project types.
   */
  setRestrictions(): void {
    this.allowedPSFeaturesList = this.user?.psFeatures?.allowedFeaturesList || [];
    this.allowedProjectTypes = this.user?.psFeatures?.project?.allowedProjectType || [];
    this.allowedProjectmanagementActions =
      this.user?.psFeatures?.project?.allowedProjectmanagementActions || [];
  }

  /**
   * Opens the create/edit project form dialog
   * @param projectTemp modal reference
   */
  openProjectModal(projectTemp: TemplateRef<any>, projectData?: any) {
    if (projectData) {
      this.isEdit = true;
      this.projectIdToUpdate = projectData._id;
      this.projectForm.patchValue({
        name: projectData.name,
        projectDescription: projectData.projectDescription,
        type: projectData.type,
      });
      this.useGPU = projectData.useGPU;
    } else {
      this.projectForm.reset();
      this.projectTypeControl.setValue('predictive_modeling');
      this.isEdit = false;
    }
    this.dialogRef = this._dialog.open(projectTemp, {
      minWidth: '50vw',
    });
  }

  /**
   * Handles the project operation as per the type - edit/new
   * @param projectDetails
   */
  onSubmitProject(projectDetails: any) {
    this.isEdit ? this.updateProjectDetails(projectDetails) : this.createNewProject(projectDetails);
  }

  /**
   * Creates a new project by triggering the API call
   * @param projectDetails
   */
  createNewProject(projectDetails: any): void {
    this.dialogRef.close();
    this._spinner.showSpinner();
    projectDetails.useGPU = this.useGPU;
    this.dashboardSub$ = this._dashboard.createNewProject(projectDetails).subscribe(
      (res) => {
        if (res) {
          this._spinner.stopSpinner();
          this._localStorage.setItem('project', res);
          this._router.navigate(['/project'], {
            queryParams: { projectId: res._id },
          });
        }
      },
      (err) => {
        this._toaster.error(err.error.message, 'Project Management');
        this._spinner.stopSpinner();
      }
    );
  }

  /**
   * Updates the project details by triggering API call
   * @param projectDetails
   */
  updateProjectDetails(projectDetails: any) {
    this.dialogRef.close();
    this._spinner.showSpinner();
    this.dashboardSub$ = this._dashboard
      .updateProjectDetails(projectDetails, this.projectIdToUpdate)
      .subscribe(
        (res) => {
          if (res) {
            this._spinner.stopSpinner();
            this._toaster.success('Project details updated successfully', 'Project Management');
            this.filteredProjectsList = this.filteredProjectsList.map((item: any) => {
              if (item._id === res._id) {
                item = res;
              }
              return item;
            });
          }
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error('Cannot update project details', 'Project Management');
        }
      );
  }

  /**
   * Opens the import project modal dialog
   * @param importProjectModal modal reference
   */
  openImportProjectModal(importProjectModal: TemplateRef<any>) {
    this._dialog
      .open(importProjectModal, {
        minWidth: '50vw',
      })
      .afterClosed()
      .subscribe((res) => (this.fileToUpload = null));
  }

  /**
   * Stores the file data to be uploaded into the variable
   * @param inputFile files object
   */
  handleFileInput(inputFile: any) {
    if (inputFile) {
      this.fileToUpload = inputFile[0];
    }
  }

  /**
   * Triggers the API call to import the project
   */
  uploadProject() {
    this._spinner.showSpinner();
    this.dashboardSub$ = this._dashboard.uploadProject(this.fileToUpload).subscribe(
      (res) => {
        if (res) {
          res.message === 'Imported project is empty' &&
            this._toaster.info(res.message, 'Project Management');
          res.message !== 'Imported project is empty' &&
            this._toaster.success(res.message, 'Project Management');
          this._spinner.stopSpinner();
          this.pageSize = 5;
          this.pageIndex = 0;
          this.getAllProjects();
        }
      },
      (err) => {
        this._spinner.stopSpinner();
        this._toaster.error(err.error.message || 'Server Error', 'Project Management');
      }
    );
  }

  /**
   * Imports the sample template
   * @param fileName
   */
  uploadTemplateProject(fileName: any): void {
    this._spinner.showSpinner();
    // API call
    this._dashboard
      .importTemplateProject({ fileName })
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          if (res) {
            this.dialogRef.close();
            res.message === 'Imported project is empty' &&
              this._toaster.info(res.message, 'Project Management');
            res.message !== 'Imported project is empty' &&
              this._toaster.success(res.message, 'Project Management');
            this._spinner.stopSpinner();
            this.pageSize = 5;
            this.pageIndex = 0;
            this.getAllProjects();
          }
        },
        (err) => {
          this._spinner.stopSpinner();
          this._toaster.error(err.error.message || 'Server Error', 'Project Management');
        }
      );
  }

  /**
   * Opens the select template dialog
   * @param templateModal modal reference
   */
  openTemplateModal(templateModal: TemplateRef<any>) {
    this.filterSampleProjects = this.allSampleProjects;
    this.dialogRef = this._dialog.open(templateModal, {
      minWidth: '75vw',
      maxWidth: '75vw',
    });
    this.dialogRef.afterClosed().subscribe(() => {
      this.showProjectDetails = false;
    });
  }

  /**
   * Fetches all project list from DB by triggering API
   */
  getAllProjects() {
    this._spinner.showSpinner();
    this.dashboardSub$ = this._dashboard.getAllProjectsList().subscribe(
      (res: any) => {
        if (res) {
          this.allProjectsList = res;
          /**
           * Sorts the projects list by data in ascending order
           */
          this.allProjectsList.sort((a: any, b: any) => {
            return new Date(b.createdAt).valueOf() - new Date(a.createdAt).valueOf();
          });
          this.iterator();
          // this.filteredProjectsList = this.allProjectsList.slice(
          //   this.pageIndex * this.pageSize,
          //   this.pageSize
          // );
        }
        this._dashboardComp.getProjectCount();
        this._spinner.stopSpinner();
      },
      (e: any) => {
        this._spinner.stopSpinner();
        this._toaster.error(e.error.message || 'Server Error', 'Project Management');
      }
    );
  }

  /**
   * Fetches all sample project list from DB by triggering API
   */
  getAllSampleProjects(): void {
    // API call
    this._sampleTemplate
      .getAllTemplates()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res.data) {
            this.allSampleProjects = res.data;
            /**
             * Sorts the sample projects list by data in ascending order
             */
            this.filterSampleProjects = this.allSampleProjects.sort((a: any, b: any) => {
              return new Date(b.createdAt).valueOf() - new Date(a.createdAt).valueOf();
            });
            this._spinner.stopSpinner();
          }
        },
        (e: any) => {
          this._spinner.stopSpinner();
          this._toaster.error(e.error.message || 'Server Error', 'Project Management');
        }
      );
  }

  /**
   * Function which generates initials of name and returns it
   * @param {*} name for initial to be generated
   */
  avatarChar(name: string): string {
    var initials = name.match(/\b\w/g) || [];
    return ((initials.shift() || '') + (initials.pop() || '')).toUpperCase();
  }

  /**
   * Called when the pagination operations are performed
   */
  pageChanged(event?: PageEvent): void {
    this.pageIndex = event.pageIndex;
    this.pageSize = event.pageSize;
    this.iterator();
  }

  /**
   * Slices the array if we change the page from pagination
   */
  iterator() {
    const end = (this.pageIndex + 1) * this.pageSize;
    const start = this.pageIndex * this.pageSize;
    const part = this.allProjectsList.slice(start, end);
    if (part.length) {
      this.filteredProjectsList = part;
    } else {
      this.pageIndex = 0;
      const end = (this.pageIndex + 1) * this.pageSize;
      const start = this.pageIndex * this.pageSize;
      const part = this.allProjectsList.slice(start, end);
      this.filteredProjectsList = part;
    }
  }

  /**
   * Copies the project id in clipboard
   * @param project
   */
  copyProjectId(project: any): void {
    this._toaster.success('Project Id is copied to clipboard', 'Project Management');
  }

  /**
   * Redirects to the project dashboard
   * @param project details
   */
  navigateToHome(project: any): void {
    this._localStorage.setItem('project', project);
    this._spinner.stopSpinner();
    this._router.navigate(['/project'], {
      queryParams: { projectId: project._id },
    });
  }

  /**
   * Opens the export project modal
   * @param exportProjectTemplate template referece
   * @param projectDetails selected project
   */
  exportProjectModal(exportProjectTemplate: TemplateRef<any>, projectDetails: any): void {
    this.showExportBtn = false;
    this.dialogRef = this._dialog.open(exportProjectTemplate, {
      minWidth: '50vw',
    });
    this.exportingProjectDetails = projectDetails;
    this._dashboard.generateDownloadLink(projectDetails._id).subscribe(
      (res) => {
        if (res) {
          this.showExportBtn = true;
          // this.downloadZipFile(projectDetails);
        } else {
          this._toaster.error('Cannot generate download link', 'Project Management');
        }
      },
      (err) => {
        this._toaster.error('Server Error', 'Project Management');
      }
    );
  }

  /**
   * Downloads the .zip file
   */
  downloadZipFile() {
    this._window.open(
      `${environment.serverBaseUrl}/api/projects/${this.exportingProjectDetails._id}/download`,
      '_blank'
    );
  }

  /**
   * Opens the project sharing modal
   * @param sharingTemplate template reference
   * @param project selected project
   */
  openSharingModal(sharingTemplate: TemplateRef<any>, project: any) {
    this.sharingProjectDetails = project;
    this.dialogRef = this._dialog.open(sharingTemplate, {
      minWidth: '50vw',
    });
  }

  /**
   * Shares the project to the input mail id
   * @param emailId receivers email id
   */
  shareProject(emailId: string) {
    this._dashboard.shareProject({ email: emailId }, this.sharingProjectDetails._id).subscribe(
      (res) => {
        if (res) {
          this._toaster.success('Project shared successfully', 'Project Management');
        } else {
          this._toaster.error('No user found with provided email', 'Project Management');
        }
      },
      (e: any) => {
        this._toaster.error(e.error.message, 'Project Management');
      }
    );
  }

  /**
   * Deletes the selected project
   * @param project details
   */
  deleteProject(project: any): void {
    let deleteType = 'soft';
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this project.',
      icon: 'warning',
      dangerMode: true,
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
          closeModal: true,
        },
        confirm: {
          text: 'Delete',
          visible: true,
          className: 'bg-danger',
          closeModal: true,
        },
      },
    }).then((isConfirm: any) => {
      if (isConfirm) {
        this._dashboard.deleteProject(project._id, deleteType).subscribe(
          (res) => {
            if (res) {
              //this.filteredProjectsList = this.filteredProjectsList.filter((item) => { item._id !== project._id });
              this.getAllProjects();
              // this.pageSize = 5;
              // this.pageIndex = 0;

              this._toaster.success(res.message, 'Project Management');
            } else {
              this._toaster.error('Cannot delete the project', 'Project Management');
            }
          },
          (err) => {
            this._toaster.success('Server error', 'Project Management');
          }
        );
      }
    });
  }

  /**
   * Opents the chat-box modal pop-up
   * @param project details
   */
  openChatBox(chatBoxTemplate: TemplateRef<any>, project: any): void {
    this.commentsProjectDetails = project;
    this.dialogRef = this._dialog.open(chatBoxTemplate, {
      minWidth: '50vw',
    });

    this._dashboard.fetchProjectComments(this.commentsProjectDetails._id).subscribe(
      (res) => {
        if (res) {
          this.commentsArray = res;
        } else {
          this._toaster.error('Cannot fetch comments', 'Project Management');
        }
      },
      (err) => {
        this._toaster.error('Server Error', 'Project Management');
      }
    );
  }

  /**
   * Adds new comment to selected project by triggering the API
   * @param message comment message
   */
  submitComment(message: string) {
    if (message) {
      const msgObj = {
        message: message,
        displayName: this.user.username,
      };
      this._dashboard.addProjectComments(msgObj, this.commentsProjectDetails._id).subscribe(
        (res) => {
          if (res) {
            this.commentMessage = '';
            this.commentsArray.push(res);
          } else {
            this._toaster.error('Cannot add comment', 'Project Management');
          }
        },
        (err) => {
          this._toaster.error('Server Error', 'Project Management');
        }
      );
    } else {
      this._snackbar.open('Please enter some message', 'OK');
    }
  }

  /**
   * Handles the GPU option from project listing checkbox
   */
  handleGpuOption(project: any): void {
    this._dashboard
      .updateProjectDetails(project, project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          if (res) {
            this.pageSize = 5;
            this.pageIndex = 0;
            this.getAllProjects();
          } else {
            this._toaster.error('Cannot update project details', 'Project Management');
          }
        },
        (err: any) => {
          this._toaster.error(`Error - ${err.error.message}`, 'Project Management');
        }
      );
  }

  /**
   * Finds pscore machines to set GPU details
   */
  findMachineDetails() {
    this._dashboard
      .getMachineDetails()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          if (res.length) {
            this._localStorage.setItem('isGpuAvailable', res.isGpuAvailable);
            this.isGpuAvailable = res.isGpuAvailable ? true : false;
          }
          this._spinner.stopSpinner();
        },
        (err: any) => {
          this._spinner.stopSpinner();
          this._toaster.error(`Error - ${err.error.message}`, 'Project Management');
        }
      );
  }

  /**
   * Returns the project domain name
   * @param data sample project details
   */
  getProjectDomain = (data: any) =>
    DomainList.find((i: any) => i.value === data.domain)?.name || '-';

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event, searchFor: string): void {
    this.searchString = (event.target as HTMLInputElement)?.value;
    if (searchFor === 'projects') {
      // Filters the created projects list
      if (this.searchString?.length >= 3) {
        this.filteredProjectsList = this.allProjectsList.filter(
          (item: any) => item.name.search(new RegExp(this.searchString, 'i')) > -1
        );
      } else {
        const end = (this.pageIndex + 1) * this.pageSize;
        const start = this.pageIndex * this.pageSize;
        const part = this.allProjectsList.slice(start, end);
        this.filteredProjectsList = part;
      }
    }
    // Filters the sample projects within whole list
    else if (
      searchFor === 'templates' &&
      this.searchString?.length >= 3 &&
      this.domainFilter === 'all'
    ) {
      if (this.searchString) {
        this.filterSampleProjects = this.allSampleProjects.filter(
          (item: any) =>
            item.name.search(new RegExp(this.searchString, 'i')) > -1 ||
            item.domain.search(new RegExp(this.searchString, 'i')) > -1 ||
            item.description.search(new RegExp(this.searchString, 'i')) > -1 ||
            item.tags.includes(this.searchString)
        );
      } else {
        this.filterSampleProjects = this.allSampleProjects;
      }
    }
    // Filters the sample projects as per the selected domain
    else if (searchFor === 'domain' && !this.searchString && this.domainFilter !== 'all') {
      if (this.domainFilter === 'all') {
        this.filterSampleProjects = this.allSampleProjects;
      } else {
        this.filterSampleProjects = this.allSampleProjects.filter(
          (item: any) => item.domain === this.domainFilter
        );
      }
    }
    // Filters the sample projects within the selected domain list
    else if (
      searchFor === 'templates' &&
      this.searchString?.length >= 3 &&
      this.domainFilter !== 'all'
    ) {
      if (this.searchString) {
        const tempArray = [...new Set(this.filterSampleProjects)];
        const foundElements = tempArray.filter(
          (item: any) =>
            item.name.search(new RegExp(this.searchString, 'i')) > -1 ||
            item.domain.search(new RegExp(this.searchString, 'i')) > -1 ||
            item.description.search(new RegExp(this.searchString, 'i')) > -1 ||
            item.tags.includes(this.searchString)
        );
        foundElements.length && (this.filterSampleProjects = foundElements);
      } else {
        this.filterSampleProjects = this.allSampleProjects.filter(
          (item: any) => item.domain === this.domainFilter
        );
      }
    }
    // Filters the sample projects with all list
    else if (
      (searchFor === 'templates' || searchFor === 'domain') &&
      !this.searchString &&
      this.domainFilter === 'all'
    ) {
      this.filterSampleProjects = this.allSampleProjects;
    }
    // Resets the sample projects list as per the selected domain
    else if (searchFor === 'templates' && !this.searchString && this.domainFilter !== 'all') {
      this.filterSampleProjects = this.allSampleProjects.filter(
        (item: any) => item.domain === this.domainFilter
      );
    }
  }

  /**
   * Unsubscribes the subscriptions only if they were subscribed
   */
  ngOnDestroy(): void {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
