import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ThemePalette } from '@angular/material/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { SampleDatasetComponent } from '../../sample-dataset/sample-dataset.component';
import { SampleDatasetService } from '../../sample-dataset/sample-dataset.service';

@Component({
  selector: 'app-dataset-form',
  templateUrl: './dataset-form.component.html',
  styleUrls: ['./dataset-form.component.scss'],
})
export class DatasetFormComponent implements OnInit {
  // Progress spinner settings
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  /** Default variables */
  isEdit: Boolean = false;
  showSpinner: Boolean = false;
  domainList: Array<object> = [
    { name: 'Predictive Modeling', value: 'predictive_modeling' },
    { name: 'Timeseries', value: 'timeseries' },
    { name: 'Clustering', value: 'clustering' },
    { name: 'Other', value: 'other' },
  ];

  datasetForm: FormGroup;
  user: any;
  fileToUpload: any;
  alldatasetList: Array<any>;
  datasetData: any;
  allowedPSFileUploadSize: number;

  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    private _fb: FormBuilder,
    private _toaster: ToastrService,
    private _tempDialogRef: MatDialogRef<SampleDatasetComponent>,
    private _localStorage: LocalStorageService,
    private _dataset: SampleDatasetService,
    private _spinner: SpinnerService,
    private _snackbar: MatSnackBar
  ) {
    /** dataset form instance creation */
    this.datasetForm = this._fb.group({
      name: ['', [Validators.required, Validators.pattern('^[^\\s][A-Za-z0-9_\\- ]+$')]],
      domain: ['', Validators.required],
      description: ['', Validators.required],
    });
    this.alldatasetList = this.dialogData.alldatasetList;
    this.datasetData = this.dialogData.datasetData;
    this.prefillAndResetForm(this.datasetData);
  }

  ngOnInit(): void {
    this.user = this._localStorage.getItem('user');
    this.allowedPSFileUploadSize = this.user?.psFeatures?.allowedPSFileUploadSize || 50;
  }

  /**
   * Prefills or reset the dataset form as required
   * @param datasetData selected dataset details
   */
  prefillAndResetForm(datasetData: any): void {
    if (datasetData) {
      this.isEdit = true;
      this.datasetForm.patchValue({
        name: datasetData.name,
        domain: datasetData.domain,
        description: datasetData.description,
      });
      this.fileToUpload = this.datasetData.file;
    } else {
      this.datasetForm.reset();
      this.isEdit = false;
    }
  }

  /**
   * Handles the form submition
   * @param formValue dataset details
   */
  onSubmitDataset(formValue: any): void {
    this._spinner.showSpinner();
    if (this.isEdit) {
      this.updateDatasetDetails(formValue);
    } else {
      this.createNewDataset(formValue);
    }
  }

  /**
   * Creates a new user into the DB
   * @param formValue
   */
  createNewDataset(formValue: any): void {
    formValue.file = this.fileToUpload.name;
    formValue.createdBy = this.user._id;
    // API call
    this._dataset
      .createDataset(this.fileToUpload, formValue)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._spinner.stopSpinner();
            this._tempDialogRef.close({ data: res.data, isEdit: this.isEdit });
            this._toaster.success('Sample dataset is created successfully', 'Sample datasets');
          } else {
            this._toaster.error('Cannot create a sample dataset', 'Sample datasets');
          }
        },
        (err: any) => {
          this._spinner.stopSpinner();
          this._tempDialogRef.close(null);
          this._toaster.error(err.error.error.message || 'Server Error', 'Sample datasets');
        }
      );
  }

  /**
   * Updates the dataset details
   * @param formValue
   */
  updateDatasetDetails(formValue: any): void {
    formValue.file = this.fileToUpload.name;
    // API call
    this._dataset
      .updateDataset(formValue, this.datasetData._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res.data) {
            this._spinner.stopSpinner();
            this._tempDialogRef.close({ data: res.data, isEdit: this.isEdit });
            this._toaster.success('Sample dataset updated successfully', 'Sample datasets');
          } else {
            this._spinner.stopSpinner();
            this._tempDialogRef.close({ data: res.data, isEdit: this.isEdit });
            this._toaster.error('Can not update sample dataset successfully', 'Sample datasets');
          }
        },
        (err: any) => {
          this._spinner.stopSpinner();
          this._tempDialogRef.close(null);
          this._toaster.error('Server Error', 'Sample datasets');
        }
      );
  }

  /**
   * Stores the file data to be uploaded into the variable
   * @param inputFile files object
   */
  handleFileInput(inputFile: any) {
    const ext = inputFile[0].name.split('.').pop();
    if (inputFile && ['csv', 'xlsx', 'xls', 'pkl', 'pk', 'pl', 'pickle'].includes(ext)) {
      if (inputFile[0].size < this.allowedPSFileUploadSize * 1024 * 1024) {
        let isDuplicate: boolean = false;
        if (!this.isEdit && this.alldatasetList) {
          this.alldatasetList.forEach((item: any) => {
            if (item.file === inputFile[0].name) {
              this._snackbar.open('File already exist, Please upload another file', 'OK');
              isDuplicate = true;
            }
          });
          if (isDuplicate) {
            this.fileToUpload = null;
          } else {
            this.fileToUpload = inputFile[0];
          }
        } else {
          this.fileToUpload = inputFile[0];
        }
      } else {
        this._snackbar.open(
          'File size should be less than ' + this.allowedPSFileUploadSize + 'Mb.',
          'OK',
          {
            horizontalPosition: 'right',
            verticalPosition: 'bottom',
          }
        );
      }
    } else {
      this._snackbar.open(`.${ext} file type is not supported`, 'OK');
    }
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
