import { SharedModule } from '../../shared/shared.module';
import { LayoutModule } from '../../layout/layout.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DashboardComponent } from './dashboard.component';
import { RouterModule, Routes } from '@angular/router';
import { ProjectManagementComponent } from './project-management/project-management.component';
import { CreateConnectionComponent } from './create-connection/create-connection.component';
import { ModelDeploymentComponent } from './model-deployment/model-deployment.component';
import { SchedularComponent } from './schedular/schedular.component';
import { LogsComponent } from './logs/logs.component';
import { PscoreNodesComponent } from './pscore-nodes/pscore-nodes.component';
import { ConnectionFormComponent } from './create-connection/connection-form/connection-form.component';
import { UsersComponent } from './users/users.component';
import { ReportsComponent } from './reports/reports.component';
import { HelpComponent } from './help/help.component';
import { UsersFormComponent } from './users/users-form/users-form.component';
import { UsecaseListComponent } from './usecase-list/usecase-list.component';
import { UsecaseComponent } from './usecase-list/usecase/usecase.component';
import { DomainComponent } from './usecase-list/domain/domain.component';
import { UploadModelComponent } from './upload-model/upload-model.component';
import { ClipboardModule } from '@angular/cdk/clipboard';
import { AuthGuard } from 'src/app/core/auth/auth.guard';
import { PlanExpiredAuthGuard } from 'src/app/core/auth/plan-expired-auth.guard';
import { PlansComponent } from './plans/plans.component';
import { PaymentComponent } from './plans/payment/payment.component';
import { SampleTemplatesComponent } from './sample-templates/sample-templates.component';
import { TemplateFormComponent } from './sample-templates/template-form/template-form.component';
import { ProfileComponent } from '../profile/profile.component';
import { SampleDatasetComponent } from './sample-dataset/sample-dataset.component';
import { DatasetFormComponent } from './sample-dataset/dataset-form/dataset-form.component';
import { AlgorithmsComponent } from './algorithms/algorithms.component';
import { AlgorithmFormComponent } from './algorithms/algorithm-form/algorithm-form.component';
import { ManagePlansComponent } from './manage-plans/manage-plans.component';
import { SnowflakeAndS3FormComponent } from './create-connection/snowflake-and-s3-form/snowflake-and-s3-form.component';
import { ProjectHandlerComponent } from './project-management/project-handler/project-handler.component';
const routes: Routes = [
  {
    path: '',
    component: DashboardComponent,
    canActivateChild: [AuthGuard],
    children: [
      { path: '', component: ProjectManagementComponent },
      { path: 'connection', component: CreateConnectionComponent },
      { path: 'modeldeploy', component: ModelDeploymentComponent },
      { path: 'scheduler', component: SchedularComponent },
      { path: 'logs', component: LogsComponent },
      { path: 'pscorenodes', component: PscoreNodesComponent },
      { path: 'user', component: UsersComponent },
      { path: 'report', component: ReportsComponent },
      { path: 'manageplans', component: ManagePlansComponent },
      { path: 'help', component: HelpComponent },
      { path: 'usecase', component: UsecaseListComponent },
      { path: 'usecase', component: UsecaseComponent },
      { path: 'domain', component: DomainComponent },
      { path: 'upload', component: UploadModelComponent },
      { path: 'template', component: SampleTemplatesComponent },
      { path: 'dataset', component: SampleDatasetComponent },
      { path: 'profile', component: ProfileComponent },
      { path: 'algorithm', component: AlgorithmsComponent },
    ],
  },
  {
    path: 'plans',
    component: DashboardComponent,
    canActivate: [PlanExpiredAuthGuard],
    children: [
      {
        path: '',
        component: PlansComponent,
      },
    ],
  },
];

@NgModule({
  declarations: [
    DashboardComponent,
    ProjectManagementComponent,
    CreateConnectionComponent,
    ModelDeploymentComponent,
    SchedularComponent,
    LogsComponent,
    PscoreNodesComponent,
    ConnectionFormComponent,
    UsersComponent,
    ReportsComponent,
    HelpComponent,
    UsersFormComponent,
    UsecaseListComponent,
    UsecaseComponent,
    DomainComponent,
    UploadModelComponent,
    PlansComponent,
    PaymentComponent,
    SampleTemplatesComponent,
    TemplateFormComponent,
    SampleDatasetComponent,
    DatasetFormComponent,
    AlgorithmsComponent,
    AlgorithmFormComponent,
    ManagePlansComponent,
    SnowflakeAndS3FormComponent,
    ProjectHandlerComponent,
  ],
  imports: [
    CommonModule,
    SharedModule,
    RouterModule.forChild(routes),
    LayoutModule,
    ClipboardModule,
  ],

  exports: [RouterModule],
})
export class DashboardModule {}
