import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
@Injectable({
    providedIn: 'root'
})
export class UsecaseListService {
    serverBaseUrl: string = environment.serverBaseUrl;
    formData: FormData = new FormData();
    constructor(private _http: HttpClient) { }

    /**
   * Fetches the usecase list from DB
   */
    getUsecaseList(): Observable<any> {
        return this._http.get(`${this.serverBaseUrl}/api/v2/usecase`);
    }

    /**
     * Creates a new usecase
     * @param usecaseDetails 
     */
    createNewUsecase(usecaseDetails: any): Observable<any> {
        return this._http.post(`${this.serverBaseUrl}/api/v2/usecase`, usecaseDetails);
    }

    /**
     * Updates the usecase
     * @param usecaseDetails 
     */
    updatedUsecase(usecaseDetails: any, usecaseId: string): Observable<any> {
        return this._http.put(`${this.serverBaseUrl}/api/v2/usecase/${usecaseId}`, usecaseDetails);
    }

    /**
     * Deletes the usecase from DB
     * @param usecaseId 
     */
    deleteUsecase(usecaseId: string): Observable<any> {
        return this._http.delete(`${this.serverBaseUrl}/api/v2/usecase/${usecaseId}`);
    }


    /**
  * Fetches the domain list from DB
  */
    getDomainList(): Observable<any> {
        return this._http.get(`${this.serverBaseUrl}/api/v2/usecase/domain`);
    }

    /**
     * Creates a new domain
     * @param domainDetails 
     */
    createNewDomain(domainDetails: any): Observable<any> {
        return this._http.post(`${this.serverBaseUrl}/api/v2/usecase/domain`, domainDetails);
    }

    /**
     * Updates the domain
     * @param domainDetails 
     */
    updatedDomain(domainDetails: any, domainId: string): Observable<any> {
        return this._http.put(`${this.serverBaseUrl}/api/v2/usecase/domain/${domainId}`, domainDetails);
    }

    /**
     * Deletes the domain from DB
     * @param domainId 
     */
    deleteDomain(domainId: string): Observable<any> {
        return this._http.delete(`${this.serverBaseUrl}/api/v2/usecase/domain/${domainId}`);
    }
    /**
    * Fetches training list from db
    */
    getTrainingList(projectId: string): Observable<any> {
        return this._http.get(`${this.serverBaseUrl}/api/projects/${projectId}/training/list`);
    }

    /**
   * Fetches model list from db
   */
    getModelList(trainingId: string): Observable<any> {
        return this._http.get(`${this.serverBaseUrl}/api/training/${trainingId}/models/list`);
    }
}
