import { UtilsService } from './../../../shared/utils.service';
import { AlgorithmFormComponent } from './algorithm-form/algorithm-form.component';
import { AlgorithmsService } from './algorithms.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Subject } from 'rxjs-compat';
import { ToastrService } from 'ngx-toastr';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { takeUntil } from 'rxjs/operators';
import { HttpErrorResponse } from '@angular/common/http';
const swal = require('sweetalert');
@Component({
  selector: 'app-algorithms',
  templateUrl: './algorithms.component.html',
  styleUrls: ['./algorithms.component.scss'],
})
export class AlgorithmsComponent implements OnInit {
  /** mat-table settings */
  dataSource: MatTableDataSource<[]>;
  @ViewChild('algoPaginator') algoPaginator: MatPaginator;
  @ViewChild('algoSort') algoSort: MatSort;

  deletedDataSource: MatTableDataSource<[]>;
  @ViewChild('deletedAlgoPaginator') deletedAlgoPaginator: MatPaginator;
  @ViewChild('deletedAlgoSort') deletedAlgoSort: MatSort;
  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();
  pageSizeOptions: Array<number> = [10, 15, 20, 30];
  displayedColumns: Array<string> = [];
  deletedAlgoColumns: Array<string> = [];
  allAlgorithmsList: Array<any>;
  allDeletedAloritmList: Array<any>;

  constructor(
    public _dialog: MatDialog,
    private _algorithm: AlgorithmsService,
    private _toaster: ToastrService,
    private _spinner: SpinnerService,
    public _utils: UtilsService
  ) {
    this.displayedColumns = ['srNo', 'name', 'type', 'createdAt', 'updatedAt', 'status', 'actions'];
    this.deletedAlgoColumns = ['srNo', 'name', 'type', 'updatedAt', 'actions'];
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getAllAlgosList();
  }

  /**
   * Fetches the all algorithms list from DB
   */
  getAllAlgosList() {
    // API call
    this._algorithm
      .getAllAlgorithms()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._spinner.stopSpinner();
            this.allAlgorithmsList = res.filter((i: any) => !i.isDeleted);
            this.allDeletedAloritmList = res.filter((i: any) => i.isDeleted);
            // Assign the data to the data source for the table to render
            this.dataSource = new MatTableDataSource(this.allAlgorithmsList);
            this.dataSource.paginator = this.algoPaginator;
            this.dataSource.sort = this.algoSort;

            this.deletedDataSource = new MatTableDataSource(this.allDeletedAloritmList);
            this.deletedDataSource.paginator = this.deletedAlgoPaginator;
            this.deletedDataSource.sort = this.deletedAlgoSort;
          } else {
            this._spinner.stopSpinner();
            this._toaster.error('Cannot fetch templates list', 'Algorithms');
          }
        },
        (err: HttpErrorResponse) => {
          this._spinner.stopSpinner();
          this._toaster.error(err.error.message || 'Server Error', 'Algorithms');
        }
      );
  }

  restoreAlgorithm(algorithm): void {
    swal({
      title: 'Are you sure?',
      text: 'Restore the deleted algorithm.',
      icon: 'warning',
      dangerMode: true,
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
          closeModal: true,
        },
        confirm: {
          text: 'Restore',
          visible: true,
          className: 'bg-primary',
          closeModal: true,
        },
      },
    }).then((isConfirm: any) => {
      if (isConfirm) {
        this._spinner.showSpinner();
        this._algorithm
          .updateAlgorithm({ isDeleted: false }, algorithm._id)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe(
            (res: any) => {
              if (res) {
                this.getAllAlgosList();
                this.allDeletedAloritmList = this.allDeletedAloritmList.filter((item) => {
                  return item._id !== res._id;
                });
                this._toaster.success(`${res.name} restored successfully`, 'Algorithms');
                // Assign the data to the data source for the table to render
                this.deletedDataSource = new MatTableDataSource(this.allDeletedAloritmList);
                this.deletedDataSource.paginator = this.deletedAlgoPaginator;
                this.deletedDataSource.sort = this.deletedAlgoSort;
                this.deletedDataSource._updateChangeSubscription();
              } else {
                this._toaster.error('Cannot restore the algorithm', 'Algorithms');
              }
              this._spinner.stopSpinner();
            },
            (err: HttpErrorResponse) => {
              this._spinner.stopSpinner();
              this._toaster.error(err.error.message || 'Server Error', 'Algorithms');
            }
          );
      }
    });
  }
  /**
   * Opens the add new algorithm form dialog
   * @param userData
   */
  openAlgorithmForm(algoData?: any): void {
    const dialogRef = this._dialog.open(AlgorithmFormComponent, {
      maxWidth: '90vw',
      data: {
        algoData: algoData || null,
        allAlgoList: this.allAlgorithmsList,
      },
    });
    dialogRef.afterClosed().subscribe((algoData) => {
      if (algoData && algoData.data) {
        if (algoData.isEdit) {
          // Updates the data in table and algos list
          this.dataSource.data = this.dataSource.data.map((i: any) => {
            i._id === algoData.data._id ? (i = algoData.data) : null;
            return i;
          });
          this.allAlgorithmsList = this.allAlgorithmsList.map((i: any) => {
            i._id === algoData.data._id ? (i = algoData.data) : null;
            return i;
          });
        } else {
          this.dataSource.data.push(algoData.data);
        }
        this.dataSource._updateChangeSubscription();
      }
    });
  }

  /**
   * Handler for delete operation
   * @param algoData
   */
  deleteHandler(algoData: any): void {
    this.deleteAlgo(algoData);
  }

  /**
   * Called when the disable/enable toggled
   * @param event
   */
  disableAlgo(event: any, data: any): void {
    this._spinner.showSpinner();
    this._algorithm
      .disableAlgorithm(data._id, event.checked)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._toaster.success(
              `${res.name} ${res.status ? 'ENABLED' : 'DISABLED'}`,
              'Algorithms'
            );
            // Update the array
            this.allAlgorithmsList = this.allAlgorithmsList.filter((i: any) => {
              if (i._id === res._id) {
                i.status = res.status;
              }
              return i;
            });
            // Assign the data to the data source for the table to render
            this.dataSource.data = this.allAlgorithmsList;
            this.dataSource.paginator = this.algoPaginator;
            this.dataSource.sort = this.algoSort;
            this.dataSource._updateChangeSubscription();
          } else {
            this._spinner.stopSpinner();
            this._toaster.error(`Cannot ${event.checked} the algorithm`, 'Algorithms');
          }
          this._spinner.stopSpinner();
        },
        (err: HttpErrorResponse) => {
          this._spinner.stopSpinner();
          this._toaster.error(err.error.message || 'Server Error', 'Algorithms');
        }
      );
  }

  /**
   * Soft deletes the selected algorithm
   * @param algoData
   */
  deleteAlgo(algoData: any): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this algorithm.',
      icon: 'warning',
      dangerMode: true,
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
          closeModal: true,
        },
        confirm: {
          text: 'Delete',
          visible: true,
          className: 'bg-danger',
          closeModal: true,
        },
      },
    }).then((isConfirm: any) => {
      if (isConfirm) {
        this._spinner.showSpinner();
        this._algorithm
          .softDeleteAlgorithm(algoData._id)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe(
            (res: any) => {
              if (res) {
                this._toaster.success(`${res.name} deleted successfully`, 'Algorithms');
                // Update the array
                this.allAlgorithmsList = this.allAlgorithmsList.filter(
                  (i: any) => i._id !== algoData._id
                );
                this.allDeletedAloritmList.push(algoData);
                // Assign the data to the data source for the table to render
                this.dataSource.data = this.allAlgorithmsList;
                this.dataSource.paginator = this.algoPaginator;
                this.dataSource.sort = this.algoSort;
                this.dataSource._updateChangeSubscription();

                this.deletedDataSource = new MatTableDataSource(this.allDeletedAloritmList);
                this.deletedDataSource.paginator = this.deletedAlgoPaginator;
                this.deletedDataSource.sort = this.deletedAlgoSort;
                this.deletedDataSource._updateChangeSubscription();
              } else {
                this._toaster.error('Cannot delete the algorithm', 'Algorithms');
              }
              this._spinner.stopSpinner();
            },
            (err: HttpErrorResponse) => {
              this._spinner.stopSpinner();
              this._toaster.error(err.error.message || 'Server Error', 'Algorithms');
            }
          );
      }
    });
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event, tableName: string): void {
    const filterValue = (event.target as HTMLInputElement).value;
    if (tableName == 'algorithms') {
      this.dataSource.filter = filterValue.trim().toLowerCase();
      if (this.dataSource.paginator) {
        this.dataSource.paginator.firstPage();
      }
    } else {
      this.deletedDataSource.filter = filterValue.trim().toLowerCase();
      if (this.deletedDataSource.paginator) {
        this.deletedDataSource.paginator.firstPage();
      }
    }
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
