import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { DatasetFormComponent } from './dataset-form/dataset-form.component';
import { SampleDatasetService } from './sample-dataset.service';
const swal = require('sweetalert');

@Component({
  selector: 'app-sample-dataset',
  templateUrl: './sample-dataset.component.html',
  styleUrls: ['./sample-dataset.component.scss'],
})
export class SampleDatasetComponent implements OnInit {
  dataSource: MatTableDataSource<[]>;
  @ViewChild('DatasetPaginator') DatasetPaginator: MatPaginator;
  @ViewChild('DatasetSort') DatasetSort: MatSort;

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();
  pageSizeOptions: Array<number> = [3, 5, 10, 15, 20];
  displayedColumns: Array<string> = [];
  allDatasetList: Array<any>;

  constructor(
    public _dialog: MatDialog,
    private _toaster: ToastrService,
    private _dataset: SampleDatasetService,
    private _spinner: SpinnerService
  ) {
    this.displayedColumns = ['srNo', 'name', 'domain', 'createdAt', 'updatedAt', 'actions'];
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getDatasetsList();
  }

  /**
   * Fetches the Datasets list from DB
   */
  getDatasetsList() {
    // API call
    this._dataset
      .getAllDatasets()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._spinner.stopSpinner();
            this.allDatasetList = res.data;
            // Assign the data to the data source for the table to render
            this.dataSource = new MatTableDataSource(res.data);
            this.dataSource.paginator = this.DatasetPaginator;
            this.dataSource.sort = this.DatasetSort;
          } else {
            this._spinner.stopSpinner();
            this._toaster.error('Cannot fetch Datasets list', 'Sample Datasets');
          }
        },
        (err: HttpErrorResponse) => {
          this._spinner.stopSpinner();
          this._toaster.error(err.error.message || 'Server Error', 'Sample Datasets');
        }
      );
  }

  /**
   * Deletes the selected Dataset
   * @param DatasetData
   */
  deleteDataset(DatasetData: any): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this Dataset.',
      icon: 'warning',
      dangerMode: true,
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
          closeModal: true,
        },
        confirm: {
          text: 'Delete',
          visible: true,
          className: 'bg-danger',
          closeModal: true,
        },
      },
    }).then((isConfirm: any) => {
      if (isConfirm) {
        this._dataset
          .deleteDataset(DatasetData._id)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe(
            (res: any) => {
              if (res) {
                // this.getUsersList();
                this._toaster.success('Dataset deleted successfully', 'Sample Datasets');
                this.dataSource.data = this.dataSource.data.filter(
                  (i: any) => i._id !== DatasetData._id
                );
                this.allDatasetList = this.allDatasetList.filter(
                  (i: any) => i._id !== DatasetData._id
                );
                this.dataSource._updateChangeSubscription();
              } else {
                this._toaster.error('Cannot delete the Dataset', 'Sample Datasets');
              }
            },
            (err: HttpErrorResponse) => {
              this._toaster.error(err.error.message || 'Server Error', 'Sample Datasets');
            }
          );
      }
    });
  }

  /**
   * Opens the create new Dataset form dialog
   * @param userData
   */
  openCreateDatasetModal(datasetData?: any): void {
    const dialogRef = this._dialog.open(DatasetFormComponent, {
      data: {
        datasetData: datasetData || null,
        allDatasetList: this.allDatasetList || [],
      },
    });
    dialogRef.afterClosed().subscribe((tempData) => {
      if (tempData && tempData.data) {
        if (tempData.isEdit) {
          this.dataSource.data = this.dataSource.data.map((i: any) => {
            i._id === tempData.data._id ? (i = tempData.data) : null;
            return i;
          });
          this.allDatasetList = this.allDatasetList.map((i: any) => {
            i._id === tempData.data._id ? (i = tempData.data) : null;
            return i;
          });
        } else {
          this.dataSource.data.push(tempData.data);
        }
        this.dataSource._updateChangeSubscription();
      }
    });
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
