import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class SampleTemplatesService {
  serverBaseUrl = environment.serverBaseUrl;

  constructor(private _http: HttpClient) {}

  /**
   * Creates a new sample template
   * @param reqObj
   * @returns
   */
  createTemplate(file: any, formValue: any): Observable<any> {
    const formData: FormData = new FormData();
    formData.append('file', file);
    formData.append('data', JSON.stringify(formValue));
    return this._http.post(`${this.serverBaseUrl}/api/sampletemplates`, formData);
  }

  /**
   * Fetches the templates list
   */
  getAllTemplates(): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/sampletemplates`);
  }

  /**
   * Deletes the template from DB
   * @param templateId
   */
  deleteTemplate(templateId: string): Observable<any> {
    return this._http.delete(`${this.serverBaseUrl}/api/sampletemplates/${templateId}`);
  }

  /**
   * Updates the template details
   * @param userId
   */
  updateTemplate(templateData: any, templateId: string): Observable<any> {
    return this._http.put(`${this.serverBaseUrl}/api/sampletemplates/${templateId}`, templateData);
  }
}
