import { LocalStorageService } from './../../../shared/local-storage.service';
import { environment } from './../../../../environments/environment';
import { UsersService } from './users.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { UsersFormComponent } from './users-form/users-form.component';
import { SpinnerService } from 'src/app/shared/spinner.service';
import * as moment from 'moment';
const swal = require('sweetalert');
@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss'],
})
export class UsersComponent implements OnInit {
  dataSource: MatTableDataSource<[]>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild('myTable') table: MatTable<any>;

  /** Subscription variables */
  usersSub$: Subscription;

  pageSizeOptions: Array<number> = [3, 5, 10, 15, 20];
  displayedColumns: Array<string> = [];
  env = environment;
  user: any;

  constructor(
    public _dialog: MatDialog,
    private _users: UsersService,
    private _toaster: ToastrService,
    private _spinner: SpinnerService,
    private _localStorage: LocalStorageService
  ) {
    this.user = this._localStorage.getItem('user');
    this.displayedColumns = this.user.roles[0] == 'super_admin' ? ['srNo', 'userName', 'name', 'roles', 'email', 'isEnabled', 'validity', 'actions'] : ['srNo', 'userName', 'name', 'roles', 'email', 'actions'];
  }

  ngOnInit(): void {
    this._spinner.showSpinner();
    this.getUsersList();
  }

  /**
   * Fetches the users list from DB
   */
  getUsersList() {
    this.usersSub$ = this._users.getUsersList().subscribe(
      (res) => {
        if (res) {
          this._spinner.stopSpinner();
          res = res.map(val => {
            if(val.validity) { 
              val.validity = Math.abs(moment().diff(val.validity, 'days')) + 1;
            }
            return val;
          });
          // Assign the data to the data source for the table to render
          this.dataSource = new MatTableDataSource(res);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          this._spinner.stopSpinner();
          this._toaster.error('Cannot fetch users list', 'Users Setting');
        }
      },
      (err) => {
        this._spinner.stopSpinner();
        this._toaster.error('Server Error', 'Users Setting');
      }
    );
  }

  /**
   * Deletes the selected user
   * @param userData
   */
  deleteUser(userData: any): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this user account!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        this.usersSub$ = this._users.deleteUser(userData._id).subscribe(
          (res: any) => {
            if (res) {
              this.getUsersList();
              this._toaster.success('User delete successfully');
            } else {
              this._toaster.error('Cannot delete user', 'Users Setting');
            }
          },
          (err: any) => {
            this._toaster.error('Server Error', 'Users Setting');
          }
        );
      }
    });
  }

  /**
   * Called when the disable/enable toggled
   * @param event
   */
  disableUser(event: any, data: any): void {
    data.isEnabled = event.checked;
    data.validity = data.validity ? moment().add(Number(data.validity)+1 , 'days').format('YYYY-MM-DD') : null
    !data.validity && delete data.validity;
    this.usersSub$ = this._users.updateUser(data, data._id).subscribe(
      (res: any) => {
        if (res) {
          data.isEnabled && this._toaster.success('User enabled successfully', 'Users Setting');
          !data.isEnabled && this._toaster.success('User disabled successfully', 'Users Setting');
        } else {
          this._toaster.error('Cannot update user details', 'Users Setting');
        }
      },
      (err: any) => {
        this._toaster.error('Server Error', 'Users Setting');
      }
    );
    data.validity = Math.abs(moment().diff(data.validity, 'days'));
  }

  /**
   * Opens the create new user form dialog
   * @param userData
   */
  openUserFormModal(userData?: any) {
    const dialogRef = this._dialog.open(UsersFormComponent, {
      minWidth: '50vw',
      data: {
        userData: userData,
      },
    });

    dialogRef.afterClosed().subscribe((userDetails) => {
      this.getUsersList();
    });
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.usersSub$) {
      this.usersSub$.unsubscribe();
    }
  }
}
