import { AlgorithmsService } from './../algorithms.service';
import { AlgorithmsComponent } from './../algorithms.component';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { CustomValidators } from 'ngx-custom-validators';
import { takeUntil } from 'rxjs/operators';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-algorithm-form',
  templateUrl: './algorithm-form.component.html',
  styleUrls: ['./algorithm-form.component.scss'],
})
export class AlgorithmFormComponent implements OnInit {
  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  /** Default variables */
  algoTypes: Array<any> = [
    { name: 'Regression', value: 'regression' },
    { name: 'Classification', value: 'classification' },
    { name: 'Timeseries', value: 'timeseries' },
    { name: 'Clustering', value: 'clustering' },
  ];
  // driftMetrics: Array<any> = [
  //   { name: 'Accuracy', value: 'accuracy' },
  //   { name: 'ROC', value: 'roc' },
  //   { name: 'F1 score', value: 'f1_score' },
  //   { name: 'Log loss error', value: 'log_loss_error' },
  // ];
  // isDriftMetrics: boolean = false;
  isEdit: Boolean = false;
  showBinaryOpts: boolean = false;
  showMulticlassOpts: boolean = false;
  showMultilabelOpts: boolean = false;
  showPlotConfigOpts: boolean = false;
  showOtherOpts: boolean = false;
  timeseriesHpt: boolean = false;

  algorithmForm: FormGroup;
  plotConfigForm: FormGroup;
  advancedOptForm: FormGroup;
  metaDataForm: FormGroup;
  binaryForm: FormGroup;
  multiclassForm: FormGroup;
  multilabelForm: FormGroup;
  otherForm: FormGroup;
  user: any;
  allAlgoList: Array<any>;
  algoData: any;
  algoToUpdate: any;

  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    private _fb: FormBuilder,
    private _toaster: ToastrService,
    private _tempDialogRef: MatDialogRef<AlgorithmsComponent>,
    private _localStorage: LocalStorageService,
    private _algorithm: AlgorithmsService,
    private _spinner: SpinnerService,
    private _snackbar: MatSnackBar
  ) {
    this.allAlgoList = this.dialogData.allAlgoList;
    this.algoData = this.dialogData.algoData;
    this.createFormInstances();
    this.prefillAndResetForm(this.algoData);
  }

  ngOnInit(): void {
    this.user = this._localStorage.getItem('user');
  }

  /**
   * Prefills or reset the algorithm form as required
   * @param algoData selected algorithm details
   */
  prefillAndResetForm(algoData: any): void {
    if (algoData) {
      this.algoToUpdate = algoData;
      this.isEdit = true;
      // algoData?.driftMetrics?.length ? (this.isDriftMetrics = true) : null;
      this.algorithmForm.patchValue({
        name: algoData.name,
        useGPU: algoData.useGPU,
        type: algoData.type,
        multilabel: algoData.multilabel,
        url: algoData.url,
        // driftMetrics: algoData.driftMetrics,
      });
      if (algoData.plotCfg) {
        this.showPlotConfigOpts = true;
        if (this.algoData.plotCfg.binary) {
          this.showBinaryOpts = true;
          this.binaryForm.patchValue({ ...this.algoData.plotCfg.binary });
        }
        if (this.algoData.plotCfg.multiLabel) {
          this.showMultilabelOpts = true;
          this.multilabelForm.patchValue({ ...this.algoData.plotCfg.multiLabel });
        }
        if (this.algoData.plotCfg.multiClass) {
          this.showMulticlassOpts = true;
          this.multiclassForm.patchValue({ ...this.algoData.plotCfg.multiClass });
        }
        if (
          !this.algoData.plotCfg.binary &&
          !this.algoData.plotCfg.multiLabel &&
          !this.algoData.plotCfg.multiClass
        ) {
          this.showOtherOpts = true;
          this.otherForm.patchValue({ ...this.algoData.plotCfg });
          this.timeseriesHpt = this.algoData.plotCfg.hpt;
        }
      }
    } else {
      this.isEdit = false;
    }
  }

  onResetAlgorithm() {
    console.log("m called******************************", this.algoData);
    if (this.algoData !== null) {
      this.algoToUpdate = this.algoData;
      this.isEdit = true;
      // algoData?.driftMetrics?.length ? (this.isDriftMetrics = true) : null;
      this.algorithmForm.patchValue({
        name: this.algoData.name,
        useGPU: this.algoData.useGPU,
        type: this.algoData.type,
        multilabel: this.algoData.multilabel,
        url: this.algoData.url,
        // driftMetrics: algoData.driftMetrics,
      });
      if (this.algoData.plotCfg) {
        this.showPlotConfigOpts = true;
        if (this.algoData.plotCfg.binary) {
          this.showBinaryOpts = true;
          this.binaryForm.patchValue({ ...this.algoData.plotCfg.binary });
        }
        if (this.algoData.plotCfg.multiLabel) {
          this.showMultilabelOpts = true;
          this.multilabelForm.patchValue({ ...this.algoData.plotCfg.multiLabel });
        }
        if (this.algoData.plotCfg.multiClass) {
          this.showMulticlassOpts = true;
          this.multiclassForm.patchValue({ ...this.algoData.plotCfg.multiClass });
        }
        if (
          !this.algoData.plotCfg.binary &&
          !this.algoData.plotCfg.multiLabel &&
          !this.algoData.plotCfg.multiClass
        ) {
          this.showOtherOpts = true;
          this.otherForm.patchValue({ ...this.algoData.plotCfg });
          this.timeseriesHpt = this.algoData.plotCfg.hpt;
        }
      } 
    } else {
      this.multiclassForm.reset();
      this.binaryForm.reset();
      this.multilabelForm.reset();
     // this.advancedOptForm.reset();
      this.otherForm.reset();
      this.algorithmForm.reset();
      //this.plotConfigForm.reset();
      this.isEdit = false;
    }
  }
  /**
   * Handles the form submition
   */
  async onSubmitAlgorithm(formValue: any): Promise<any> {
    const reqObj = await this.generateReqObj();
    this._spinner.showSpinner();
    if (this.isEdit) {
      this.updateAlgoDetails(reqObj);
    } else {
      this.createNewAlgo(reqObj);
    }
  }

  /**
   * Creates a new algorithm into the DB
   * @param formValue
   */
  createNewAlgo(formValue: any): void {
    // API call
    this._algorithm
      .addNewAlgorithm(formValue)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._spinner.stopSpinner();
            this._tempDialogRef.close({ data: res, isEdit: this.isEdit });
            this._toaster.success('New algorithm created successfully', 'Algorithms');
          } else {
            this._toaster.error('Cannot create a new algorithm', 'Algorithms');
          }
        },
        (err: any) => {
          this._spinner.stopSpinner();
          this._toaster.error(err.error.message || 'Server Error', 'Algorithms');
          this._tempDialogRef.close(null);
        }
      );
  }

  /**
   * Updates the algorithm details
   * @param formValue
   */
  updateAlgoDetails(formValue: any): void {
    // API call
    this._algorithm
      .updateAlgorithm(formValue, this.algoToUpdate._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._spinner.stopSpinner();
            this._tempDialogRef.close({ data: res, isEdit: this.isEdit });
            this._toaster.success(`${formValue.name} updated successfully`, 'Algorithms');
          } else {
            this._spinner.stopSpinner();
            this._tempDialogRef.close({ data: res, isEdit: this.isEdit });
            this._toaster.error(`Cannot update ${formValue.name} details`, 'Algorithms');
          }
        },
        (err: HttpErrorResponse) => {
          if (err.error.message.includes('duplicate')) {
            this._spinner.stopSpinner();
            this._tempDialogRef.close(null);
            this._toaster.error('Algorithm name already exists', 'Algorithms'); 
          } else {
            this._spinner.stopSpinner();
            this._tempDialogRef.close(null);
            this._toaster.error(err.error.message || 'Server Error', 'Algorithms');
          }  
        }
      );
  }

  /**
   * Called when the drift metrics is changed
   * @param event
   */
  // driftMetricsChange(event: any): void {
  //   if (event.checked) {
  //     this.algorithmForm.get('driftMetrics').setValidators(Validators.required);
  //     this.algorithmForm
  //       .get('driftMetrics')
  //       .updateValueAndValidity({ onlySelf: true, emitEvent: true });
  //   } else {
  //     this.algorithmForm.get('driftMetrics').reset();
  //     this.algorithmForm
  //       .get('driftMetrics')
  //       .updateValueAndValidity({ onlySelf: true, emitEvent: true });
  //   }
  // }

  /**
   * Creates the required object structure for DB
   * @param reqObj
   */
  async generateReqObj(): Promise<any> {
    let finalObj: any = {
      ...this.algorithmForm.value,
    };
    if (this.showPlotConfigOpts) {
      finalObj.plotCfg = {};
      if (this.showBinaryOpts) {
        finalObj.plotCfg.binary = {
          ...this.binaryForm.value,
        };
      }
      if (this.showMulticlassOpts) {
        finalObj.plotCfg.multiClass = {
          ...this.multiclassForm.value,
        };
      }
      if (this.showMultilabelOpts) {
        finalObj.plotCfg.multiLabel = {
          ...this.multilabelForm.value,
        };
      }
      if (this.algorithmForm.get('type').value === 'regression') {
        finalObj.plotCfg = {
          ...this.otherForm.value,
        };
      }
    }
    if (this.algorithmForm.get('type').value === 'timeseries') {
      finalObj.plotCfg = {
        hpt: this.timeseriesHpt,
      };
    }
    return finalObj;
  }

  /**
   * Creates all the form instances
   */
  createFormInstances() {
    /** Algorithm form instance */
    this.algorithmForm = this._fb.group({
      name: [null, [Validators.required, Validators.pattern('^[^\\s][A-Za-z0-9_\\- ]+$')]],
      useGPU: [{ value: false, disabled: this.algoData ? true : false }],
      type: [null, Validators.required],
      multilabel: [
        {
          value: false,
          disabled:
            this.algoData?.id === '54bagging_classifier' ||
              this.algoData?.id === '56boosting_classifier'
              ? true
              : false,
        },
      ],
      url: [null, CustomValidators.url],
      // driftMetrics: [null],
    });
    /** Binary form instance */
    this.binaryForm = this._fb.group({
      rocAucScore: [false],
      liftGain: [false],
      prCurve: [false],
      limeReport: [false],
      mathewsCoeff: [false],
      advancedOpt: this._fb.group({
        featureCoeff: [false],
        featureImp: [false],
        decisionTreeGraph: [false],
        advancedAlgo: [false],
      }),
    });
    /** Multiclass form instance */
    this.multiclassForm = this._fb.group({
      rocAucScore: [false],
      liftGain: [false],
      prCurve: [false],
      limeReport: [false],
      mathewsCoeff: [false],
      advancedOpt: this._fb.group({
        featureCoeff: [false],
        featureImp: [false],
        decisionTreeGraph: [false],
        advancedAlgo: [false],
      }),
    });
    /** Multilabel form instance */
    this.multilabelForm = this._fb.group({
      rocAucScore: [false],
      liftGain: [false],
      prCurve: [false],
      limeReport: [false],
      mathewsCoeff: [false],
      advancedOpt: this._fb.group({
        featureCoeff: [false],
        featureImp: [false],
        decisionTreeGraph: [false],
        advancedAlgo: [false],
      }),
    });
    /** Other form instance */
    this.otherForm = this._fb.group({
      limeReport: [false],
      advancedOpt: this._fb.group({
        featureCoeff: [false],
        featureImp: [false],
        decisionTreeGraph: [false],
        advancedAlgo: [false],
      }),
    });
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
